import { BASE_URL_PostsAPI } from '../../shared/Urls';

export const POST_COMMENT = 'POST_COMMENT';
export const POST_COMMENT_RESPONSE = 'POST_COMMENT_RESPONSE';
export const ERROR_POSTING_COMMENT = 'ERROR_POSTING_COMMENT';

export const GET_COMMENTS = 'GET_COMMENTS';
export const RECEIVE_COMMENTS = 'RECEIVE_COMMENTS';
export const ERROR_RECEIVING_COMMENTS = 'ERROR_RECEIVING_COMMENTS';

export const LIKE_COMMENT = 'LIKE_COMMENT';
export const LIKE_COMMENT_RESPONSE = 'LIKE_COMMENT_RESPONSE';
export const ERROR_LIKING_COMMENT = 'ERROR_LIKING_COMMENT';

//*********************************POST_COMMENT********************************************* */
export const postComment = () => ({
    type: POST_COMMENT
});

export const postCommentResponse = (data) => ({
    type: POST_COMMENT_RESPONSE,
    data: data
});

export const errorPostingComment = (error) => ({
    type: ERROR_POSTING_COMMENT,
    error: error
});

//********************************END:: POST_COMMENT ::END***************************************** */

//*********************************LIKE_COMMENT**************************************************** */
export const likeComment = () => ({
    type: LIKE_COMMENT
});

export const likeCommentResponse = () => ({
    type: LIKE_COMMENT_RESPONSE
});

export const errorLikingComment = (error) => ({
    type: ERROR_LIKING_COMMENT,
    error: error
});
//********************************END:: LIKE_COMMENT ::END**************************************** */
//*********************************GET_COMMENTS********************************************* */
export const getComments = () => ({
    type: GET_COMMENTS
});

export const receiveComments = (data) => ({
    type: RECEIVE_COMMENTS,
    data: data
});

export const errorReceivingComments = (error) => ({
    type: ERROR_RECEIVING_COMMENTS,
    error: error
});

//********************************END:: GET_COMMENTS ::END***************************************** */

export function AddComment(postId, comment) {
    var url = BASE_URL_PostsAPI + 'Comments/Posts/' + postId;
    return dispatch => {
        dispatch(postComment());
        return fetch(url, {
            method: 'POST',
            body: JSON.stringify(comment),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(json => {
                dispatch(postCommentResponse(json));
            }).catch(err => {
                dispatch(errorPostingComment(err));
            });
    }
}

export function fetchCommentsForPost(postId) {
    var url = BASE_URL_PostsAPI + 'Comments/Posts/' + postId;
    return dispatch => {
        dispatch(getComments());
        return fetch(url, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(json => {
                console.log('Len = ' + json.length)
                dispatch(receiveComments(json));
            }).catch(err => {
                dispatch(errorReceivingComments(err));
            });
    }
}

export function likeCommentById(postId, commentId, user) {
    var url = BASE_URL_PostsAPI + 'Comments/likes/' + commentId + '/posts/' + postId;
    return dispatch => {
        dispatch(likeComment());
        return fetch(url, {
            method: 'POST',
            body: JSON.stringify(user),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(json => {
                dispatch(likeCommentResponse(json));
            }).catch(err => {
                dispatch(errorLikingComment(err));
            });
    }
}