import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Period from './Period';
import PeriodTime from './PeriodTime';
import PeriodDay from './PeriodDay';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import Chip from '@material-ui/core/Chip';

// Imports: Redux Actions
import { getTimetable } from '../redux/actions/calenderActions';

const styles = theme => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(0),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        fontSize: 10,
    },
});

class Timetable extends Component {

    render() {
        const { classes } = this.props;
        let count = 0;
        let bColor = '#f1f1f1';
        const timetableView = this.props.timetable.map((timetableRow) => {
            count++;
            if (count % 2 === 0) {
                bColor = '#e0ebeb'
            } else {
                bColor = '#f1f1f1';
            }
            return (
                <Grid key={timetableRow.periodSlotId} container item xs={12} spacing={1} style={{ backgroundColor: bColor }}>
                    <PeriodTime key={'times_' + timetableRow.periodSlotId} bColor={bColor} startTime={timetableRow.startTime} endTime={timetableRow.endTime} />
                    <Period key={'monday_' + timetableRow.periodSlotId} bColor={bColor} period={timetableRow.monday} />
                    <Period key={'tuesday_' + timetableRow.periodSlotId} bColor={bColor} period={timetableRow.tuesday} />
                    <Period key={'wednesday_' + timetableRow.periodSlotId} bColor={bColor} period={timetableRow.wednesday} />
                    <Period key={'thursday_' + timetableRow.periodSlotId} bColor={bColor} period={timetableRow.thursday} />
                    <Period key={'friday_' + timetableRow.periodSlotId} bColor={bColor} period={timetableRow.friday} />
                </Grid>
            )
        });
        return (
            <div className={classes.root}>
                <div>
                    <span style={{ color: 'red', fontWeight: 'bold', fontSize: 12 }}>NOTE: </span>
                    <span style={{ color: 'red', fontSize: 12 }}>It your responsibility to confirm/validate timetable below!</span>
                    <Chip variant="outlined" color="secondary" size="small" label="Report incorrect/missing Timetable" onClick={() => { this.goToFeedback() }} />
                </div>
                <br />
                <Grid container spacing={1}>
                    <Grid key={'row_days'} container item xs={12} spacing={1} style={{ backgroundColor: bColor }}>
                        <Grid item xs={2} style={{ height: '100%', width: '100%', minHeight: 30 }}>
                            <Paper className={classes.paper} style={{ height: '100%', width: '100%', backgroundColor: 'green', borderRadius: 20 }}
                                onClick={() => { this.addModule() }}
                            >
                                <span style={{ fontWeight: 'bold' }}>Add Module</span>
                            </Paper>
                        </Grid>
                        <PeriodDay key={'monday_'} bColor={bColor} day={'Mon'} />
                        <PeriodDay key={'tuesday_'} bColor={bColor} day={'Tue'} />
                        <PeriodDay key={'wednesday_'} bColor={bColor} day={'Wed'} />
                        <PeriodDay key={'thursday_'} bColor={bColor} day={'Thur'} />
                        <PeriodDay key={'friday_'} bColor={bColor} day={'Fri'} />
                    </Grid>
                    {timetableView}
                </Grid>
            </div>
        );
    }

    goToFeedback(){
        this.props.history.push('/Feedback');
    }

    addModule() {
        if (this.props.university === null || this.props.university === undefined || this.props.university.id === undefined
            || this.props.university.id === '' || this.props.university.id === '0000-0000-000000000000') {
            this.props.history.push('/University')
        } else if (this.props.university.timetablePerCampus === true || this.props.university.timetablePerCampus === 'true') {
            this.props.history.push('/Campus/' + this.props.university.id)
        } else {
            this.props.history.push('/institutions/' + this.props.university.id + '/type/universities')
        }
    }
    getOrUpdateTimetable() {
        if (this.props.university === null || this.props.university === undefined || this.props.university.id === '' ||
            this.props.university.id === undefined || this.props.university.id === '0000-0000-000000000000') {
            return;
        }
        let coursesIds = [];
        for (let course of this.props.courses) {
            coursesIds.push(course.id);
        }
        this.props.getCalender({
            universityId: this.props.university.id,
            coursesIds: coursesIds
        });
    }

    componentDidUpdate() {
        this.getOrUpdateTimetable();
    }
    componentDidMount() {
        this.getOrUpdateTimetable();
    }

}

// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        timetable: state.calenderReducer.timetable,
        university: state.calenderReducer.university,
        courses: state.calenderReducer.courses
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action
    return {
        getCalender: (body) => dispatch(getTimetable(body))
    };
};

// Exports
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(withRouter(Timetable)));
