import {
    ADD_NOTE,
    REMOVE_NOTE,
    UPDATE_NOTE
} from '../actions/noteActions'

// Initial State
const initialState = {
    notes: [],
};

// Reducers (Modifies The State And Returns A New State)
const noteReducer = (state = initialState, action) => {
    switch (action.type) {

        case ADD_NOTE: {
            return {
                ...state,
                notes: [...state.notes, action.data],
            }
        }

        case UPDATE_NOTE: {
            return {
                ...state,
                notes: updateNotes(state.notes, action.data),
            }
        }

        case REMOVE_NOTE: {
            return {
                ...state,
                notes: state.notes.filter(item => item.id !== action.noteId),
            }
        }

        // Default
        default: {
            return state;
        }
    }
};

const updateNotes = (notes, newNote) => {
    let newNotes = [];
    notes.forEach(element => {
        if (element.id === newNote.id) {
            newNotes.push(newNote);
        } else {
            newNotes.push(element);
        }
    });
    return newNotes;
};

// Exports
export default noteReducer;