import {
  REQUEST_CALENDER,
  RECEIVE_CALENDER,
  ERROR_REQUESTING_CALENDER,
  ADD_MODULE,
  REMOVE_MODULE,
  GET_CALENDER,
  REMOVE_PERIOD,
  RESOLVE_CONFLICT,
  ERROR_RECEIVING_MODULES,
  RECEIVE_MODULES,
  SEARCH_MODULES,
  GET_UNREAD_MESSAGES_COUNT,
  SET_UNIVERSITY,
  REPORT_CALENDER
} from '../actions/calenderActions'

// Initial State
const initialState = {
  timetable: [],
  courses: [],
  university: {
    id: '0000-0000-000000000000',
    name: 'Your Institution',
    adsDetails: {
      bannerAd: 'ca-app-pub-5631714136940575/3298705317',
      nativeAdvancedAd: 'ca-app-pub-5631714136940575/4838053665'
    }
  },
  campusId: '',
  mustRefreshCalender: true,
  error: '',
  hasHttpError: false,
  isFetching: false,
  isSearching: false,
  hasErrorSearching: false,
  searchResults: []
};

// Reducers (Modifies The State And Returns A New State)
const calenderReducer = (state = initialState, action) => {
  switch (action.type) {

    case ERROR_RECEIVING_MODULES: {
      return {
        ...state,
        error: action.error,
        hasErrorSearching: true,
        isSearching: false
      }
    }

    case SEARCH_MODULES: {
      return {
        ...state,
        error: '',
        hasErrorSearching: false,
        isSearching: true
      }
    }

    case RECEIVE_MODULES: {
      return {
        ...state,
        error: '',
        searchResults: action.data,
        hasErrorSearching: false,
        isSearching: false
      }
    }

    //****************************************************************************************** */
    case REQUEST_CALENDER: {
      return {
        ...state,
        error: '',
        mustRefreshCalender: true,
        hasHttpError: false,
        isFetching: true
      }
    }

    case GET_CALENDER: {
      return {
        ...state,
        error: '',
        searchResults: [],
        mustRefreshCalender: false,
        hasHttpError: false,
        isFetching: true
      }
    }

    case RECEIVE_CALENDER: {
      return {
        ...state,
        error: '',
        mustRefreshCalender: false,
        timetable: action.data,
        hasHttpError: false,
        isFetching: false
      }
    }

    case ERROR_REQUESTING_CALENDER: {
      return {
        ...state,
        mustRefreshCalender: true,
        error: action.error,
        hasHttpError: true,
        isFetching: false
      }
    }

    case ADD_MODULE: {
      return {
        ...state,
        mustRefreshCalender: true,
        courses: [...state.courses, action.course]
      }
    }

    case GET_UNREAD_MESSAGES_COUNT: {
      return {
        ...state,
      }
    }

    case SET_UNIVERSITY: {
      return {
        ...state,
        university: action.university
      }
    }

    case REMOVE_MODULE: {
      return {
        ...state,
        mustRefreshCalender: true,
        courses: state.courses.filter(item => item.id !== action.courseId),
      }
    }

    case REMOVE_PERIOD: {
      return {
        ...state,
        mustRefreshCalender: false,
        timetable: removePeriod(state.timetable, action.periodId)
      }
    }

    case RESOLVE_CONFLICT: {
      return {
        ...state,
        mustRefreshCalender: false,
        timetable: getTimetableWithResolvedConflictForGivenPeriod(action.periodId, action.course, state.timetable),
      }
    }

    // Default
    default: {
      return state;
    }
  }
};

const removePeriod = (timetable, periodId) => {
  var emptyPeriod = { isEmptySlot: true };
  var slots = new Array();
  timetable.forEach(element => {
    slots.push(element);
  });
  for (var i = 0; i < slots.length; i++) {
    if (slots[i].monday.periodId === periodId) {
      slots[i].monday = emptyPeriod;
      break;
    }
    if (slots[i].tuesday.periodId === periodId) {
      slots[i].tuesday = emptyPeriod;
      break;
    }
    if (slots[i].wednesday.periodId === periodId) {
      slots[i].wednesday = emptyPeriod;
      break;
    }
    if (slots[i].thursday.periodId === periodId) {
      slots[i].thursday = emptyPeriod;
      break;
    }
    if (slots[i].friday.periodId === periodId) {
      slots[i].friday = emptyPeriod;
      break;
    }
  }
  return slots;
};

const getTimetableWithResolvedConflictForGivenPeriod = (periodId, course, timetable) => {
  var slots = new Array();
  timetable.forEach(element => {
    slots.push(element);
  });
  for (var i = 0; i < slots.length; i++) {
    if ((slots[i].monday.hasConflict === true || slots[i].monday.hasConflict === 'true')
      && conflictsHasPeriodId(slots[i].monday.conflicts, periodId)) {
      slots[i].monday = course;
      break;
    }
    if ((slots[i].tuesday.hasConflict === true || slots[i].tuesday.hasConflict === 'true')
      && conflictsHasPeriodId(slots[i].tuesday.conflicts, periodId)) {
      slots[i].tuesday = course;
      break;
    }
    if ((slots[i].wednesday.hasConflict === true || slots[i].wednesday.hasConflict === 'true')
      && conflictsHasPeriodId(slots[i].wednesday.conflicts, periodId)) {
      slots[i].wednesday = course;
      break;
    }
    if ((slots[i].thursday.hasConflict === true || slots[i].thursday.hasConflict === 'true')
      && conflictsHasPeriodId(slots[i].thursday.conflicts, periodId)) {
      slots[i].thursday = course;
      break;
    }
    if ((slots[i].friday.hasConflict === true || slots[i].friday.hasConflict === 'true')
      && conflictsHasPeriodId(slots[i].friday.conflicts, periodId)) {
      slots[i].friday = course;
      break;
    }
  }
  return slots;
}

const conflictsHasPeriodId = (conflicts, periodId) => {
  var isFound = false;
  conflicts.forEach(element => {
    if (element.periodId === periodId) {
      isFound = true;
    }
  });
  return isFound;
};

// Exports
export default calenderReducer;