import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Iframe from 'react-iframe'
import ReactDOM from 'react-dom'

// Screen: GenericFileCard
class GenericFileCard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            iFrameHeight: '300px'
        }
    }
    render() {
        const { params } = this.props.match;
        return (
            <div style={{ height: '100%' }}>
                <Iframe url={'http://sitholewb.co.za/FilesAPI/api/Storage/' + params.id + '/view'}
                    width="100%"
                    height={this.state.iFrameHeight}
                    id="myId"
                    display="block"
                    allowFullScreen={true}
                    position="relative"
                />
            </div>
        )
    }

    componentDidMount() {
        try {
            this.setState({ iFrameHeight: window.innerHeight + 'px' });
        } catch{
            this.setState({ iFrameHeight: '400px' });
        }

    }

}

// Styles
const styles = theme => ({
    root: {
        flexGrow: 1,
    }
});
export default withStyles(styles)(GenericFileCard);