import { BASE_URL_PostsAPI } from '../../shared/Urls';

export const ADD_POST = 'ADD_POST';
export const ADD_POST_RESPONSE = 'ADD_POST_RESPONSE';
export const ERROR_ADDING_POST = 'ERROR_ADDING_POST';

export const GET_POSTS = 'GET_POSTS';
export const RESET_POSTS = 'RESET_POSTS';
export const RECEIVE_POSTS = 'RECEIVE_POSTS';
export const ERROR_RECEIVING_POSTS = 'ERROR_RECEIVING_POSTS';

export const LIKE_POST = 'LIKE_POST';
export const LIKE_POST_RESPONSE = 'LIKE_POST_RESPONSE';
export const ERROR_LIKING_POST = 'ERROR_LIKING_POST';

//*********************************ADD_POST********************************************* */
export const addPost = () => ({
    type: ADD_POST
});

export const addPostResponse = (data) => ({
    type: ADD_POST_RESPONSE,
    data: data
});

export const errorAddingPost = (error) => ({
    type: ERROR_ADDING_POST,
    error: error
});

//********************************END:: ADD_POST ::END***************************************** */

//*********************************LIKE_POST**************************************************** */
export const likePost = () => ({
    type: LIKE_POST
});

export const likePostResponse = (data) => ({
    type: LIKE_POST_RESPONSE,
    data: data
});

export const errorLikingPost = (error) => ({
    type: ERROR_LIKING_POST,
    error: error
});
//********************************END:: LIKE_POST ::END************************************* */

//*********************************GET_POSTS********************************************* */
export const getPosts = (categoryId) => ({
    type: GET_POSTS,
    categoryId: categoryId
});

export const resetPosts = () => ({
    type: RESET_POSTS,
});

export const receivePosts = (data) => ({
    type: RECEIVE_POSTS,
    data: data
});

export const errorReceivingPosts = (error) => ({
    type: ERROR_RECEIVING_POSTS,
    error: error
});

//********************************END:: GET_POSTS ::END***************************************** */

export function sendPost(categoryId, post) {
    var url = BASE_URL_PostsAPI + 'Posts/' + categoryId;
    return dispatch => {
        dispatch(addPost());
        return fetch(url, {
            method: 'POST',
            body: JSON.stringify(post),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(json => {
                dispatch(addPostResponse(json));
            }).catch(err => {
                dispatch(errorAddingPost(err));
            });
    }
}

export function fetchGetPosts(categoryId, body) {
    var url = BASE_URL_PostsAPI + 'Posts/' + categoryId + '/query';
    return dispatch => {
        dispatch(getPosts(categoryId));
        return fetch(url, {
            method: 'POST',
            body: JSON.stringify(body),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(json => {
                dispatch(receivePosts(json));
            }).catch(err => {
                dispatch(errorReceivingPosts(err));
            });
    }
}

export function likePostById(postId, user) {
    var url = BASE_URL_PostsAPI + 'Posts/likes/' + postId;
    return dispatch => {
        dispatch(likePost());
        return fetch(url, {
            method: 'POST',
            body: JSON.stringify(user),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(json => {
                dispatch(likePostResponse(json));
            }).catch(err => {
                dispatch(errorLikingPost(err));
            });
    }
}