import {
  GET_UNIVERSITIES,
  RECEIVE_UNIVERSITIES,
  ERROR_GETTING_UNIVERSITIES
} from '../actions/universityActions'

// Initial State
const initialState = {
  error: '',
  isFetching: false,
  universities:[]
};

// Reducers (Modifies The State And Returns A New State)
const universitiesReducer = (state = initialState, action) => {
  switch (action.type) {

    case GET_UNIVERSITIES: {
      return {
        ...state,
        error: '',
        hasHttpError: false,
        isFetching: true
      }
    }

    case RECEIVE_UNIVERSITIES: {
      return {
        ...state,
        error: '',
        universities: action.data,
        hasHttpError: false,
        isFetching: false
      }
    }

    case ERROR_GETTING_UNIVERSITIES: {
      return {
        ...state,
        error: action.error,
        hasHttpError: true,
        isFetching: false
      }
    }

    // Default
    default: {
      return state;
    }
  }
};


// Exports
export default universitiesReducer;