// Imports: Dependencies
import React, { Component } from 'react';
import { connect } from 'react-redux';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';

// Imports: Redux Actions
import { fetchSearchModules, receiveModules, addModule, removeModule, fetchAndSetUniversity } from '../redux/actions/calenderActions';

//import screens
import ActivitySpinner from './ActivitySpinner'

class Course extends Component {
    constructor(props) {
        super(props);

        this.state = {
            searchText: '',
            doneSearching: true,
            hasPressedSearch: false,
            searchResultsView: [],
            courses: [],
            showSnackbar: false,
            snackbarText: ''
        }
    }
    render() {


        if ((this.props.isSearching === true || this.props.isSearching === 'true') && this.state.doneSearching === false) {
            return (
                <ActivitySpinner />
            )
        } else {
            const { classes } = this.props;
            const searchResultsView = this.props.searchResults.map((resultValue) => {
                return (
                    <span key={'span_' + resultValue.id}>
                        <ListItem className={classes.listItem} key={resultValue.id} dense button onClick={() => { this.goToModules(resultValue) }}>
                            <ListItemText id={'text_' + resultValue.id} primary={resultValue.code} secondary={resultValue.name} />
                            <ListItemSecondaryAction>
                                {this.getActionButton(resultValue, this.props.courses)}
                            </ListItemSecondaryAction>
                        </ListItem>
                        <Divider />
                    </span>
                )
            });
            const coursesView = this.props.courses.map((resultValue) => {
                return (
                    <span key={'view_span_' + resultValue.id}>
                        <ListItem className={classes.listItem} key={'view_' + resultValue.id} dense button onClick={() => { this.goToModules(resultValue) }}>
                            <ListItemText id={'view_text_' + resultValue.id} primary={resultValue.code} secondary={resultValue.name} />
                            <ListItemSecondaryAction>
                                {this.getActionButton(resultValue, this.props.courses)}
                            </ListItemSecondaryAction>
                        </ListItem>
                        <Divider />
                    </span>
                )
            });
            return (
                <div >
                    <Snackbar
                        anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                        key={'course_snackbar'}
                        open={this.state.showSnackbar}
                        onClose={() => { this.handleClose() }}
                        message={this.state.snackbarText}
                        autoHideDuration={3000}
                        action={
                            <React.Fragment>
                                <Button color="secondary" size="small" onClick={() => { this.handleClose() }}>
                                    DISMISS
                                </Button>
                                <IconButton
                                    aria-label="close"
                                    color="inherit"
                                    onClick={() => { this.handleClose() }}
                                >
                                    <CloseIcon />
                                </IconButton>
                            </React.Fragment>
                        }
                    />


                    {(this.state.hasPressedSearch === true && this.state.searchText.length < 4) &&
                        <span style={{ color: 'red' }} >Enter 4 or more characters </span>
                    }
                    <TextField
                        id="standard-full-width"
                        label="Search Module"
                        style={{ margin: 8 }}
                        placeholder="Search Module by Name or Code... e.g MATH130"
                        fullWidth
                        margin="normal"
                        onChange={(text) => this.onChangeSearchText(text)}
                        value={this.state.searchText}
                        InputLabelProps={{
                            shrink: true,
                        }}
                    />

                    <Button variant="contained" color="primary" onClick={() => this.searchModule()}>
                        Search
                    </Button>
                    <Divider />
                    {(this.state.hasPressedSearch === true && this.state.searchText.length >= 4) &&
                        <span>Total courses is {this.props.searchResults.length}</span>
                    }
                    {(!(this.state.hasPressedSearch === true && this.state.searchText.length >= 4)) &&
                        <div>
                            <Divider />
                            <List>
                                {coursesView}
                            </List>
                        </div>
                    }
                    {(this.state.hasPressedSearch === true && this.state.searchText.length >= 4) &&
                        <div>
                            <Divider />
                            <List>
                                {searchResultsView}
                            </List>
                        </div>
                    }
                </div>
            )
        }

    }
    showSnackbar(text) {
        this.setState({ showSnackbar: true, snackbarText: text });
    };

    handleClose() {
        this.setState({ showSnackbar: false, snackbarText: '' });
    };
    onChangeSearchText(text) {
        this.setState({ hasPressedSearch: false, searchText: text.target.value });
    }

    searchModule() {
        const { params } = this.props.match;
        this.setState({ hasPressedSearch: true });
        if (this.state.searchText.length >= 4) {
            this.setState({ doneSearching: false })
            this.props.searchModules(params.id, this.state.searchText, params.routeType).then(() => {
                this.setState({ hasPressedSearch: true, doneSearching: true })
            });
        }
    }

    getActionButton(course, coursesAlreadyExists) {
        if (this.courseExists(course.id, coursesAlreadyExists) === false) {
            return (
                <Button variant="contained" color="primary" onClick={() => this.addCourse(course)}>
                    Add
                </Button>
            )
        } else {
            return (
                <Button variant="contained" color="secondary" onClick={() => this.removeCourse(course.id)}>
                    Remove
                </Button>
            )
        }
    }

    addCourse(course) {
        this.props.addModule(course);
        const newCourses = [...this.state.courses, course];
        this.setState({ courses: newCourses });
        this.props.fetchAndSetUniversity(course.universityId);
        this.showSnackbar('Module added successfully.');
    }

    removeCourse(courseId) {
        this.props.removeModule(courseId);
        var newCourses = [];
        for (let course of this.state.courses) {
            if (course.id !== courseId) {
                newCourses.push(course);
            }
        }
        this.setState({ courses: newCourses });
        this.showSnackbar('Module removed successfully.');
    }

    courseExists(courseId, coursesAlreadyExists) {
        for (let course of coursesAlreadyExists) {
            if (course.id === courseId) {
                return true;
            }
        }
        return false;
    }

    componentDidMount() {
        this.props.receiveModules([]);//Call this method to clear isSearching value
    }

}

// Styles
const useStyles = theme => ({
    root: {
        width: '100%'
    },
    listItem: {
        width: '100%',
        backgroundColor: '#f9ecf2'
    },
});


// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        searchResults: state.calenderReducer.searchResults,
        isSearching: state.calenderReducer.isSearching,
        error: state.calenderReducer.error,
        hasErrorSearching: state.calenderReducer.hasErrorSearching,
        courses: state.calenderReducer.courses,
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action
    return {
        searchModules: (campusId, body, routPath) => dispatch(fetchSearchModules(campusId, body, routPath)),
        receiveModules: (data) => dispatch(receiveModules(data)),
        addModule: (data) => dispatch(addModule(data)),
        removeModule: (data) => dispatch(removeModule(data)),
        fetchAndSetUniversity: (universityId) => dispatch(fetchAndSetUniversity(universityId))
    };
};

// Exports
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(Course));