// Imports: Dependencies
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import SchoolIcon from '@material-ui/icons/School';
import Divider from '@material-ui/core/Divider';
import { withRouter } from 'react-router-dom';
import { useLocation } from "react-router-dom";

// Imports: Redux Actions
import { fetchCampuses } from '../redux/actions/campusActions';

//import screens
import ActivitySpinner from './ActivitySpinner'


class Campus extends Component {
    render() {
        const { classes } = this.props;
        const universitiesView = this.props.campuses.map((resultValue) => {
            return (
                <span key={'span_' + resultValue.id}>
                    <ListItem className={classes.listItem} key={resultValue.id} dense button onClick={() => { this.goToModules(resultValue) }}>
                        <ListItemText id={'text_' + resultValue.id} primary={resultValue.name} />
                        <ListItemSecondaryAction>
                            <IconButton edge="end" aria-label="comments">
                                <SchoolIcon />
                            </IconButton>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <Divider />
                </span>
            )
        });
        if (this.props.isFetching === true || this.props.isFetching === 'true') {
            return (
                <ActivitySpinner />
            )
        } else {
            return (
                <List>
                    <Divider />
                    < span >Click your campus below.</span>
                    <Divider />
                    {universitiesView}
                </List>
            )
        }

    }

    goToModules(campus) {
        this.props.history.push('/institutions/' + campus.id + '/type/campuses')
    }

    componentDidMount() {
        const { params } = this.props.match;
        this.props.fetchCampuses(params.id);
    }

}

const useStyles = theme => ({
    root: {
        width: '100%'
    },
    listItem: {
        width: '100%',
        backgroundColor: '#f9ecf2'
    },
});

// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        campuses: state.campusesReducer.campuses,
        isFetching: state.campusesReducer.isFetching,
        error: state.campusesReducer.error
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action
    return {
        fetchCampuses: (id) => dispatch(fetchCampuses(id))
    };
};

// Exports
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(Campus));