// Imports: Dependencies
import { combineReducers } from 'redux';

// Imports: Reducers
import calenderReducer from './calenderReducer';
import feedbackReducer from './feedbackReducer';
import campusesReducer from './campusesReducer';
import universitiesReducer from './universitiesReducer';
import bookmarkReducer from './bookmarkReducer';
import uploadReducer from './uploadReducer';
import commentReducer from './commentReducer';
import postReducer from './postReducer';
import noteReducer from './noteReducer';
import userReducer from './userReducer';

// Redux: Root Reducer
const rootReducer = combineReducers({
  calenderReducer: calenderReducer,
  feedbackReducer: feedbackReducer,
  campusesReducer: campusesReducer,
  universitiesReducer: universitiesReducer,
  bookmarkReducer: bookmarkReducer,
  uploadReducer: uploadReducer,
  commentReducer: commentReducer,
  noteReducer: noteReducer,
  postReducer: postReducer,
  userReducer: userReducer
});

// Exports
export default rootReducer;