import { BASE_URL_CoursesAPI } from '../../shared/Urls'

export const GET_UNIVERSITIES = 'GET_UNIVERSITIES';
export const RECEIVE_UNIVERSITIES = 'RECEIVE_UNIVERSITIES';
export const ERROR_GETTING_UNIVERSITIES = 'ERROR_GETTING_UNIVERSITIES';

export const getUniversities = () => ({
    type: GET_UNIVERSITIES
});

export const receiveUniversities = (data) => ({
    type: RECEIVE_UNIVERSITIES,
    data: data
});

export const errorReceivingUniversities = (error) => ({
    type: ERROR_GETTING_UNIVERSITIES,
    error: error
});

function fetchUniversitiesFromAPI() {
    var url = BASE_URL_CoursesAPI + 'universities';
    return dispatch => {
        dispatch(getUniversities());
        return fetch(url, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(json => {
                dispatch(receiveUniversities(json));
            }).catch(err => {
                dispatch(errorReceivingUniversities(err));
            });
    }
}

export function fetchUniversities() {
    return (dispatch, getState) => {
        var university = getState().calenderReducer.university;
        if (university === null || university === undefined || university.id === undefined ||
            university.id === '' || university.id === '0000-0000-000000000000') {
            return dispatch(fetchUniversitiesFromAPI());
        }
    }
}
