import {
    POST_COMMENT,
    POST_COMMENT_RESPONSE,
    ERROR_POSTING_COMMENT,
    GET_COMMENTS,
    RECEIVE_COMMENTS,
    ERROR_RECEIVING_COMMENTS,
    LIKE_COMMENT,
    LIKE_COMMENT_RESPONSE,
    ERROR_LIKING_COMMENT
} from '../actions/commentActions'

// Initial State
const initialState = {
  comments: [],
  response:'',
  error: '',
  isFetchingComments:false,
  hasHttpErrorFetchingComments: false,
  likeCommentPendingResponse:false,
  hasHttpErrorLikingComment: false,
  postCommentPending:false,
  hasHttpErrorPostingComment: false  
};

// Reducers (Modifies The State And Returns A New State)
const commentReducer = (state = initialState, action) => {
  switch (action.type) {

    //*********************************POST_COMMENT********************************************* */
    case POST_COMMENT: {
      return {
        ...state,
        error: '',
        hasHttpErrorPostingComment: false,
        postCommentPending:true
      }
    }

    case POST_COMMENT_RESPONSE: {
      return {
        ...state,
        error: '',
        response: action.data,
        hasHttpErrorPostingComment: false,
        postCommentPending:false
      }
    }

    case ERROR_POSTING_COMMENT: {
      return {
        ...state,
        error: action.error,
        hasHttpErrorPostingComment: true,
        postCommentPending:false
      }
    }
    //********************************END:: POST_COMMENT ::END***************************************** */
    //*********************************GET_COMMENTS********************************************* */
    case GET_COMMENTS: {
      return {
        ...state,
        error: '',
        hasHttpErrorFetchingComments: false,
        isFetchingComments:true
      }
    }

    case RECEIVE_COMMENTS: {
      return {
        ...state,
        error: '',
        comments: action.data,
        hasHttpErrorFetchingComments: false,
        isFetchingComments:false
      }
    }

    case ERROR_RECEIVING_COMMENTS: {
      return {
        ...state,
        error: action.error,
        hasHttpErrorFetchingComments: true,
        isFetchingComments:false
      }
    }
    //********************************END:: POST_COMMENT ::END***************************************** */
    //*********************************LIKE_COMMENT**************************************************** */

    case LIKE_COMMENT: {
      return {
        ...state,
        error: '',
        hasHttpErrorLikingComment: false,
        likeCommentPendingResponse:true
      }
    }

    case LIKE_COMMENT_RESPONSE: {
      return {
        ...state,
        error: '',
        hasHttpErrorLikingComment: false,
        likeCommentPendingResponse:false
      }
    }

    case ERROR_LIKING_COMMENT: {
      return {
        ...state,
        error: action.error,
        hasHttpErrorLikingComment: true,
        likeCommentPendingResponse:false
      }
    }
    //********************************END:: LIKE_COMMENT ::END**************************************** */
    // Default
    default: {
      return state;
    }
  }
};


// Exports
export default commentReducer;