import { BASE_URL_FilesAPI } from '../../shared/Urls';

export const UPLOAD_FILE = 'UPLOAD_FILE';
export const UPLOAD_FILE_RESPONSE = 'UPLOAD_FILE_RESPONSE';
export const ERROR_UPLOADING_FILE = 'ERROR_UPLOADING_FILE';
export const UPLOAD_FILE_PROGRESS = 'UPLOAD_FILE_PROGRESS';


//*********************************UPLOAD_FILE**************************************************** */
export const uploadFile = () => ({
    type: UPLOAD_FILE
});

export const uploadFileResponse = (data) => ({
    type: UPLOAD_FILE_RESPONSE,
    data: data
});

export const errorUploadingFile = (error) => ({
    type: ERROR_UPLOADING_FILE,
    error: error
});

export const uploadFileProgress = (percent) => ({
    type: UPLOAD_FILE_PROGRESS,
    percent: percent
});
//*********************************END:: UPLOAD_FILE ::END **************************************************** */

export function uploadFileToStorage(fileObject) {
    var formData = new FormData();
    formData.append('file', fileObject);
    return dispatch => {
        dispatch(uploadFile())
        return fetch(BASE_URL_FilesAPI + 'Storage', {
            method: 'POST',
            body: formData
        })
            .then(response => response.json())
            .then(data => {
                dispatch(uploadFileResponse(data));
            })
            .catch(error => {
                dispatch(errorUploadingFile(error));
            })
    }
}
