import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { withRouter } from 'react-router-dom';

class Period extends Component {
    render() {
        const { classes } = this.props;
        if (this.props.period.isEmptySlot === false || this.props.period.isEmptySlot === 'false') {
            return (
                <Grid item xs={2} style={{ backgroundColor: this.props.bColor }}>
                    <Paper onClick={() => { this.goToDetails() }} className={classes.paper} style={{ height: '100%', width: '100%', whiteSpace: 'unset', backgroundColor: this.props.period.color.backgroungColor }}>
                        <span style={{ fontWeight: 'bold' }}> {this.props.period.courseCode}</span>
                        <br />
                        <span style={{ fontSize: 7 }}> {this.props.period.venueName}</span>
                    </Paper>
                </Grid>
            )
        } else {
            return (
                <Grid item xs={2} style={{ height: '100%', width: '100%', backgroundColor: this.props.bColor }}>
                    <Paper className={classes.paper} style={{ height: '100%', width: '100%', backgroundColor: this.props.bColor }}></Paper>
                </Grid>
            )
        }
    }

    goToDetails() {
        this.props.history.push("/PeriodDetails/" + this.props.period.periodId);
    }
}

// Styles
const useStyles = theme => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(0),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        fontSize: 10,
    },
});

//exports
export default (withStyles(useStyles)(withRouter(Period)));