import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

// Screen: Counter
class PeriodDay extends Component {
    render() {
        const { classes } = this.props;
        return (
            <Grid item xs={2} style={{ height: '100%', width: '100%', }}>
                <Paper className={classes.paper} style={{ height: '100%', width: '100%' }}>
                    <span style={{ fontWeight: 'bold' }}>{this.props.day}</span>
                </Paper>
            </Grid>
        )
    }
}

// Styles
const styles = theme => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(0),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        fontSize: 10,
        backgroundColor: '#9fd1d1'
    },
});
export default withStyles(styles)(PeriodDay);