import {
  GET_CAMPUSES,
  RECEIVE_CAMPUSES,
  ERROR_GETTING_CAMPUSES
} from '../actions/campusActions'

// Initial State
const initialState = {
  error: '',
  isFetching: false,
  campuses:[]
};

// Reducers (Modifies The State And Returns A New State)
const campusesReducer = (state = initialState, action) => {
  switch (action.type) {

    case GET_CAMPUSES: {
      return {
        ...state,
        error: '',
        hasHttpError: false,
        isFetching: true
      }
    }

    case RECEIVE_CAMPUSES: {
      return {
        ...state,
        error: '',
        campuses: action.data,
        hasHttpError: false,
        isFetching: false
      }
    }

    case ERROR_GETTING_CAMPUSES: {
      return {
        ...state,
        error: action.error,
        hasHttpError: true,
        isFetching: false
      }
    }

    // Default
    default: {
      return state;
    }
  }
};


// Exports
export default campusesReducer;