// Imports: Dependencies
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import EditIcon from '@material-ui/icons/Edit';
import Typography from '@material-ui/core/Typography';
import AttachmentIcon from '@material-ui/icons/Attachment';
import Chip from '@material-ui/core/Chip';

//import screens
import ActivitySpinner from './ActivitySpinner'
import PostCard from './PostCard';

// Imports: Redux Actions
import { fetchGetPosts, sendPost, resetPosts } from '../redux/actions/postActions';
import { addUpdateUser } from '../redux/actions/userActions';
import { uploadFileToStorage } from '../redux/actions/uploadActions';

// Screen: Comments
class Posts extends Component {
    constructor(props) {
        super(props);
        this.fileUpload = React.createRef();
        this.showFileUpload = this.showFileUpload.bind(this);
        this.onFileChange = this.onFileChange.bind(this);
        this.state = {
            postText: '',
            fileIsAttached: false,
            fileName: '',
            documentObject: {},
            addPostPending: false,
            noMorePosts: false
        }
    }
    render() {
        const { classes } = this.props;
        if (this.props.isFetchingPosts === true && this.props.posts.length === 0) {
            return (
                <ActivitySpinner />
            )
        } else {

            const coursesView = this.props.posts.map((resultValue) => {
                return (
                    <PostCard key={'card_' + resultValue.id} post={resultValue} />
                )
            });
            const { params } = this.props.match;
            return (
                <Fragment>
                    <span style={{ fontWeight: 'bold', fontSize: 20, backgroundColor: '#f9ecf2' }}>{params.code}.</span>
                    <Divider />
                    <br />
                    <div>
                        {
                            //***************************START :: ADD POST :: START************************************************* */
                        }
                        {(this.state.addPostPending === false) &&
                            <div>
                                <TextField
                                    id="standard-full-width"
                                    label=""
                                    style={{ margin: 8 }}
                                    placeholder="Add your post..."
                                    fullWidth
                                    multiline
                                    rows="4"
                                    margin="normal"
                                    onChange={(text) => this.onChangeText(text)}
                                    value={this.state.postText}
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                />

                                {(this.state.fileIsAttached === false) &&
                                    <React.Fragment>
                                        <div>
                                            <input style={{ display: "none" }} id="postFileId" type="file" ref={this.fileUpload} onChange={this.onFileChange} />
                                        </div>

                                        <div>
                                            <Chip
                                                icon={<AttachmentIcon />}
                                                label="attach file(image/pdf/video/etc)"
                                                clickable
                                                color="primary"
                                                size="small"
                                                variant="outlined"
                                                onClick={() => { this.showFileUpload() }}
                                            />
                                        </div>
                                    </React.Fragment>
                                }

                                {(this.state.fileIsAttached === true) &&
                                    <React.Fragment>
                                        <Chip
                                            icon={<AttachmentIcon />}
                                            label={this.state.fileName}
                                            onDelete={() => { this.removeFile() }}
                                            color="secondary"
                                            variant="outlined"
                                        />
                                        <span><br /></span>
                                    </React.Fragment>
                                }


                                <div style={{ width: '50%' }}>
                                    <span><br /></span>
                                    <Button variant="contained" color="primary" onClick={() => { this.sendPost() }}>
                                        Add Post
                                    </Button>
                                </div>

                                <br />
                            </div>
                        }
                        {(this.state.addPostPending === true) &&

                            <div>
                                <span style={{ color: 'green', fontWeight: 'bold' }}>Adding your post...</span>
                                <ActivitySpinner />
                                {(this.state.fileIsAttached === true) &&
                                    <span style={{ color: 'green', fontWeight: 'bold' }}>Attachment upload progress {this.props.percent}%...</span>
                                }
                            </div>
                        }
                        {
                            //***************************END :: ADD POST :: END************************************************* */
                        }

                        <Divider />
                        <div>
                            <List>
                                {coursesView}
                            </List>
                            {(this.props.isFetchingPosts === true && this.props.posts.length > 0) &&
                                <ActivitySpinner />
                            }
                            <br />
                            {(this.state.noMorePosts === false) &&
                                <Button variant="contained" color="primary" onClick={() => { this.loadMorePosts() }}>
                                    LOAD MORE POSTS...
                                </Button>
                            }
                            {(this.state.noMorePosts === true) &&
                                <span>No more posts to show.</span>
                            }
                            <br />
                        </div >
                    </div>
                </Fragment>
            )
        }
    }

    onFileChange(e) {
        this.setState({ fileIsAttached: true, fileName: e.target.files[0].name, documentObject: e.target.files[0] })
    }

    showFileUpload() {
        this.fileUpload.current.click();
    }

    removeFile() {
        this.setState({ fileIsAttached: false, fileName: '' })
    }

    onChangeText(text) {
        this.setState({ postText: text.target.value });
    }

    sendPost() {
        if (this.state.postText.length < 1) {
            return;
        } else {
            const { params } = this.props.match;
            this.setState({ addPostPending: true })
            if (this.state.fileIsAttached === false) {
                this.props.sendPost(params.id, {
                    body: this.state.postText,
                    addedBy: this.props.user
                }).then(() => {
                    this.setState({ postText: '' })
                    this.setState({ addPostPending: false })
                })
            } else {
                this.props.uploadFileToStorage(this.state.documentObject)
                    .then(() => {
                        this.props.sendPost(params.id, {
                            body: this.state.postText,
                            addedBy: this.props.user,
                            files: [this.props.uploadResponse]
                        }).then(() => {
                            this.setState({ postText: '', fileIsAttached: false, fileName: '', addPostPending: false })
                        })
                    });
            }


        }
    }

    loadMorePosts() {
        if (this.props.posts.length === 50) {
            const { params } = this.props.match;
            var body = {
                "beforeDate": this.props.posts[49].addedDate,
                "categoryId": params.id,
                "lastN": 50
            }
            this.props.fetchGetPosts(params.id, body);
        } else {
            this.setState({ noMorePosts: true })
        }
    }

    componentDidMount() {
        const { params } = this.props.match;
        var body = {
            "beforeDate": null,
            "categoryId": params.id,
            "lastN": 50
        }
        this.props.resetPosts();
        this.props.fetchGetPosts(params.id, body);
    }
}

// Styles
const useStyles = theme => ({
    container: {
        margin: 1,
        display: 'flex',
        flexWrap: 'nowrap'
    },
    removeFileButton: {
        fontSize: 13,
        textAlign: 'center'
    },
    removeFileCustomBtnBG: {
        backgroundColor: "#ffe0b3",
        borderRadius: 3,
        alignSelf: 'flex-start',
        borderRadius: 10,
        height: 23
    },
    addModuleButton: {
        fontSize: 13,
        textAlign: 'center'
    },
    addModuleCustomBtnBG: {
        backgroundColor: "#ffe0b3",
        borderRadius: 3,
        alignItems: 'center',
        borderRadius: 10,
        width: 220,
        alignSelf: 'flex-end',
        height: 23
    },
});

// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        posts: state.postReducer.posts,
        isFetchingPosts: state.postReducer.isFetchingPosts,
        addPostPending: state.postReducer.addPostPending,
        user: state.userReducer.user,
        uploadResponse: state.uploadReducer.response,
        percent: state.uploadReducer.percent,
        isUploadingFile: state.uploadReducer.isFetching
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action
    return {
        fetchGetPosts: (categoryId, body) => dispatch(fetchGetPosts(categoryId, body)),
        sendPost: (categoryId, body) => dispatch(sendPost(categoryId, body)),
        addUpdateUser: (user) => dispatch(addUpdateUser(user)),
        uploadFileToStorage: (obj) => dispatch(uploadFileToStorage(obj)),
        resetPosts: (categoryId) => dispatch(resetPosts(categoryId))
    };
};

// Exports
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(Posts));