import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Snackbar from '@material-ui/core/Snackbar';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import CloseIcon from '@material-ui/icons/Close';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import Chip from '@material-ui/core/Chip';

//import screens
import MediaCard from './MediaCard';

// Imports: Redux Actions
import { likeCommentById } from '../redux/actions/commentActions';

class CommentCard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hasConflict: false,
            post: {},
            showSnackbar: false,
            snackbarText: '',
            totalLikes: 0
        }
    }
    render() {
        const { classes } = this.props;
        const date = new Date(this.props.comment.addedDate);
        const displayDate = date.toDateString() + ' ' + date.toLocaleTimeString();

        return (
            <div>

                <Snackbar
                    anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                    key={'course_snackbar'}
                    open={this.state.showSnackbar}
                    onClose={() => { this.handleClose() }}
                    message={this.state.snackbarText}
                    autoHideDuration={3000}
                    action={
                        <React.Fragment>
                            <Button color="secondary" size="small" onClick={() => { this.handleClose() }}>
                                DISMISS
                                </Button>
                            <IconButton
                                aria-label="close"
                                color="inherit"
                                onClick={() => { this.handleClose() }}
                            >
                                <CloseIcon />
                            </IconButton>
                        </React.Fragment>
                    }
                />

                <Card className={classes.root}>
                    <CardHeader
                        avatar={
                            <Avatar aria-label="recipe" className={classes.avatar}>
                                <AccountCircleIcon />
                            </Avatar>
                        }
                        action={
                            <IconButton aria-label="settings">
                                <MoreVertIcon />
                            </IconButton>
                        }
                        title={this.getUserFullNames()}
                        subheader={displayDate}
                    />
                    <Divider />

                    <CardContent>
                        <Typography variant="body2" color="textSecondary" component="p">
                            {this.props.comment.body}
                        </Typography>
                    </CardContent>
                    <CardContent>
                        {((this.props.comment.files !== null && this.props.comment.files.length > 0)) &&
                            <MediaCard
                                file={this.props.comment.files[0]}
                            />
                        }
                    </CardContent>
                    <CardActions style={{ backgroundColor: '#f9ecf2' }} disableSpacing>
                        <IconButton aria-label="Like this comment" style={{ color: this.state.likeButtonBackgroundColor }} onClick={() => { this.handlePostLike() }}>
                            <FavoriteIcon />
                        </IconButton>
                        {(this.props.comment.likes.length > 0) &&
                            <Chip variant="outlined" size="small" label={this.props.comment.likes.length + " likes"} />
                        }
                    </CardActions>

                </Card>

            </div>
        );
    }


    showSnackbar(text) {
        this.setState({ showSnackbar: true, snackbarText: text });
    };

    handleClose() {
        this.setState({ showSnackbar: false, snackbarText: '' });
    };


    getUserFullNames() {
        if (this.props.comment.addedBy === null || this.props.comment.addedBy === 'null' || this.props.comment.addedBy === undefined) {
            return 'Anonymous User';
        } else {
            return this.props.comment.addedBy.fullnames;
        }
    }

    handlePostLike() {
        this.props.likeCommentById(this.props.comment.postId, this.props.comment.id, this.props.user).then(() => {
            if (this.state.likeButtonBackgroundColor === 'black') {
                this.setState({ likeButtonBackgroundColor: 'green', totalLikes: this.state.totalLikes + 1 })
            } else {
                this.setState({ likeButtonBackgroundColor: 'black', totalLikes: this.state.totalLikes - 1 })
            }
        });
    }

    existOnLikes() {
        var exist = false;
        const list = this.props.comment.likes;
        for (var i = 0; i < list.length; i++) {
            if (list[i].userId === this.props.user.userId) {
                exist = true;
            }
        }
        return exist;
    }

    sentComment() {
        if (this.state.commentText.length < 1) {
            this.showSnackbar('Input must be at least 1 letter');
            return;
        } else {
            this.setState({ addPostPending: true })
            if (this.state.fileIsAttached === false) {
                this.props.AddComment(this.props.comment.id, {
                    body: this.state.commentText,
                    addedBy: this.props.user
                }).then(() => {
                    this.setState({
                        addPostPending: false,
                        commentText: '',
                        showAddComment: false,
                        showComments: true,
                        totalComments: this.state.totalComments + 1
                    })

                })
            } else {
                this.props.uploadFileToStorage(this.state.documentObject)
                    .then(() => {
                        this.props.AddComment(this.props.comment.id, {
                            body: this.state.commentText,
                            addedBy: this.props.user,
                            files: [this.props.uploadResponse]
                        }).then(() => {
                            this.setState({
                                commentText: '',
                                fileIsAttached: false,
                                fileName: '',
                                addPostPending: false,
                                showAddComment: false,
                                showComments: true,
                                totalComments: this.state.totalComments + 1
                            })
                        })
                    });
            }
        }
    }

    componentDidMount() {
        var likeText = 'black';
        if (this.existOnLikes()) {
            likeText = 'green';
        }

        this.setState({ likeButtonBackgroundColor: likeText })
    }

}
const useStyles = theme => ({
    root: {
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: 'green',
    },
});

// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        user: state.userReducer.user,
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action
    return {
        likeCommentById: (postId, commentId, user) => dispatch(likeCommentById(postId, commentId, user))
    };
};

// Exports
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(CommentCard));
