import React, { Component } from 'react';
import './App.css';

import { BrowserRouter as Router, Route } from 'react-router-dom'
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import ForumIcon from '@material-ui/icons/Forum';
import EditIcon from '@material-ui/icons/Edit';
import BookmarkIcon from '@material-ui/icons/Bookmark';
import ScheduleIcon from '@material-ui/icons/Schedule';
import { Link, HashRouter } from 'react-router-dom';

import Timetable from './screens/Timetable';
import PeriodDetails from './screens/PeriodDetails';
import University from './screens/University';
import Campus from './screens/Campus';
import Course from './screens/Course';
import Chats from './screens/Chats';
import Notes from './screens/Notes';
import Posts from './screens/Posts';
import GenericFileCard from './screens/GenericFileCard';
import Bookmarks from './screens/Bookmarks';
import Feedback from './screens/Feedback';

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      value: 0,
      period: {}
    }
  }

  changeValue(newValue) {
    this.setState({ value: newValue })
  }

  render() {
    return (
      <HashRouter>
        <Route exact path="/" component={Timetable} />
        <Route exact path="/PeriodDetails/:id" component={PeriodDetails} />
        <Route exact path="/University" component={University} />
        <Route path="/Campus/:id" component={Campus} />
        <Route path="/institutions/:id/type/:routeType" component={Course} />
        <Route path="/Chats" component={Chats} />
        <Route path="/Notes" component={Notes} />
        <Route path="/Posts/:id/code/:code" component={Posts} />
        <Route path="/file/:id/view" component={GenericFileCard} />
        <Route path="/Bookmarks" component={Bookmarks} />
        <Route path="/Feedback" component={Feedback} />

        <BottomNavigation
          showLabels
          value={this.state.value}
          style={{
            width: '100%',
            position: 'fixed',
            bottom: 0,
          }}
        >

          <BottomNavigationAction
            component={Link}
            to="/"
            label="Timetable"
            icon={<ScheduleIcon />}
            onClick={() => { this.changeValue(0) }}
          />

          <BottomNavigationAction
            component={Link}
            to="/Chats"
            label="Chats"
            icon={<ForumIcon />}
            onClick={() => { this.changeValue(1) }}
          />

          <BottomNavigationAction
            component={Link}
            to="/Notes"
            label="Notes"
            icon={<EditIcon />}
            onClick={() => { this.changeValue(2) }}
          />

          <BottomNavigationAction
            component={Link}
            to="/Bookmarks"
            label="Bookmarks"
            icon={<BookmarkIcon />}
            onClick={() => { this.changeValue(3) }}
          />
        </BottomNavigation>


      </HashRouter>
    );
  }
}
export default App;
