import {
    ADD_BOOKMARK,
    REMOVE_BOOKMARK,
    REMOVE_ALL_BOOKMARK
} from '../actions/bookmarkActions'

// Initial State
const initialState = {
    bookmarkedPosts: [],
};

// Reducers (Modifies The State And Returns A New State)
const bookmarkReducer = (state = initialState, action) => {
    switch (action.type) {

        case ADD_BOOKMARK: {
            return {
                ...state,
                bookmarkedPosts: [...state.bookmarkedPosts, action.post],
            }
        }

        case REMOVE_BOOKMARK: {
            return {
                ...state,
                bookmarkedPosts: state.bookmarkedPosts.filter(item => item.id !== action.postId),
            }
        }

        case REMOVE_ALL_BOOKMARK: {
            return {
                ...state,
                bookmarkedPosts: [],
            }
        }

        // Default
        default: {
            return state;
        }
    }
};


// Exports
export default bookmarkReducer;