// Imports: Dependencies
import React, { Component } from 'react';
import { connect } from 'react-redux';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import EditIcon from '@material-ui/icons/Edit';

// Imports: Redux Actions
import { addNote, removeNote, updateNote } from '../redux/actions/noteActions';


class Notes extends Component {
    constructor(props) {
        super(props);

        this.state = {
            title: '',
            body: '',
            isEdit: false,
            actionType: 'viewList',
            noteToView: {}
        }
    }
    render() {
        const { classes } = this.props;

        const dateCreated = new Date(this.state.noteToView.createdDate);
        const displayDateCreated = dateCreated.toDateString() + ' ' + dateCreated.toLocaleTimeString();

        const dateModified = new Date(this.state.noteToView.lastModifiedDate);
        const displayDateModified = dateModified.toDateString() + ' ' + dateModified.toLocaleTimeString();

        if (this.state.actionType === 'viewNote') {
            return (
                <div>
                    <div>
                        <Divider />
                        <span className={classes.nameHeader}> {this.state.noteToView.title} </span>
                        <span className={classes.dates}> <span style={{ fontWeight: 'bold' }}>Created on:</span>  {displayDateCreated.toString()}</span>
                        <span className={classes.dates}>  <span style={{ fontWeight: 'bold' }}>Last modified on:</span> {displayDateModified.toString()}</span>
                        <Divider />
                        <span> <br /> <br /></span>
                        <span>{this.state.noteToView.body}</span>
                    </div>
                    <span> <br /> <br /></span>
                    <Divider />
                    <Button variant="contained" color="primary" onClick={() => { this.setState({ actionType: 'viewList' }) }}>
                        Back to Notes
                    </Button>
                </div>
            )
        } else if (this.state.actionType === 'add' || this.state.actionType === 'edit') {
            return (
                <React.Fragment>
                    <React.Fragment>
                        <span style={{ fontWeight: 'bold', fontSize: 20 }}>Title</span>
                        <TextField
                            id="standard-full-width"
                            label=""
                            style={{ margin: 8 }}
                            placeholder="Type note title here..."
                            fullWidth
                            margin="normal"
                            onChange={(text) => this.onChangeTitleText(text)}
                            value={this.state.title}
                            InputLabelProps={{
                                shrink: true,
                            }}
                        />
                        <span style={{ fontWeight: 'bold', fontSize: 20 }}>Body</span>
                        <TextField
                            id="standard-full-width"
                            label=""
                            style={{ margin: 8 }}
                            placeholder="Type your notes here..."
                            fullWidth
                            multiline
                            rows="4"
                            margin="normal"
                            onChange={(text) => this.onChangeBodyText(text)}
                            value={this.state.body}
                            InputLabelProps={{
                                shrink: true,
                            }}
                        />
                    </React.Fragment>
                    <span style={{ color: 'white' }}>_</span>
                    <div className={classes.containerActions}>
                        <Button variant="contained" color="primary" onClick={() => { this.addOrEditNote() }}>
                            Save Note
                            </Button>
                        <Button variant="contained" onClick={() => { this.setState({ actionType: 'viewList' }) }}>
                            Back to Notes
                            </Button>
                    </div>
                </React.Fragment>
            )
        } else {
            const notesView = this.props.notes.map((resultValue) => {
                const dateCreated = new Date(resultValue.createdDate);
                const displayDateCreated = dateCreated.toDateString() + ' ' + dateCreated.toLocaleTimeString();

                const dateModified = new Date(resultValue.lastModifiedDate);
                const displayDateModified = dateModified.toDateString() + ' ' + dateModified.toLocaleTimeString();
                return (
                    <span key={'span_' + resultValue.id}>
                        <ListItem className={classes.listItem} key={resultValue.id} dense button onClick={() => { this.viewNote(resultValue) }}>
                            <ListItemText id={'text_' + resultValue.id}
                                primary={resultValue.title}
                                secondary={'Modified on: ' + displayDateModified}
                            />
                            <ListItemSecondaryAction>
                                <React.Fragment>
                                    <IconButton edge="end" aria-label="edit" onClick={() => { this.addOrEditNote(resultValue, true) }} >
                                        <EditIcon />
                                    </IconButton>
                                    <IconButton edge="end" aria-label="delete" onClick={() => { this.deleteNote(resultValue.id) }} >
                                        <DeleteIcon />
                                    </IconButton>
                                </React.Fragment>
                            </ListItemSecondaryAction>
                        </ListItem>
                        <Divider />
                        <br />
                    </span>
                )
            });

            return (
                <React.Fragment>

                    <div>
                        <Divider />
                        <Button variant="contained" color="primary" onClick={() => { this.addOrEditNote({}, false) }}>
                            Add Note
                        </Button>
                    </div>
                    <Divider />
                    <List>
                        {notesView}
                    </List>
                </React.Fragment>
            )
        }
    }

    createGuid() {
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
            var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
            return v.toString(16);
        });
    }

    viewNote(note) {
        this.setState({ noteToView: note, actionType: 'viewNote' });

    }

    addOrEditNote(note, isEdit) {

        if (this.state.actionType === 'viewList') {
            if (isEdit === true) {
                this.setState({ actionType: 'edit', noteToEdit: note, isEdit: true, title: note.title, body: note.body });
            } else {
                this.setState({ actionType: 'add', isEdit: false, title: '', body: '' });
            }

        } else {
            if (this.state.title.length < 1) {

            } else {
                if (this.state.isEdit === false) {
                    const body = {
                        id: this.createGuid(),
                        createdDate: new Date(),
                        lastModifiedDate: new Date(),
                        title: this.state.title,
                        body: this.state.body
                    };
                    this.props.addNote(body);
                } else {
                    const body = {
                        id: this.state.noteToEdit.id,
                        createdDate: this.state.noteToEdit.createdDate,
                        lastModifiedDate: new Date(),
                        title: this.state.title,
                        body: this.state.body
                    };
                    this.props.updateNote(body);
                }
                this.setState({ actionType: 'viewList', isEdit: false, title: '', body: '' });
            }
        }
    }

    deleteNote(id) {
        this.props.removeNote(id);
    }

    onChangeTitleText(text) {
        this.setState({ title: text.target.value });
    }

    onChangeBodyText(text) {
        this.setState({ body: text.target.value });
    }

    componentDidMount() {
    }

}

// Styles
const useStyles = theme => ({
    container: {
        display: 'flex',
        backgroundColor: 'white',
    },
    containerActions: {
        display: 'flex',
        flexWrap: 'nowrap',
        backgroundColor: 'white',
        flexDirection: 'row',
    },
    mainContainer: {
        display: 'flex',
        flexWrap: 'nowrap',
        backgroundColor: 'white',
        flexDirection: 'column',
        alignItems: 'stretch',
    },
    nameHeader: {
        display: 'flex',
        flexWrap: 'nowrap',
        backgroundColor: 'white',
        textAlign: 'center',
        fontWeight: 'bold',
        fontSize: 25,
    },
    dates: {
        fontSize: 12,
        textAlign: 'center',
    },
    listItem: {
        width: '100%',
        backgroundColor: '#f9ecf2'
    },
});

// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        notes: state.noteReducer.notes,
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action
    return {
        addNote: (note) => dispatch(addNote(note)),
        removeNote: (noteId) => dispatch(removeNote(noteId)),
        updateNote: (note) => dispatch(updateNote(note)),
    };
};

// Exports
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(Notes));