
import { BASE_URL_CoursesAPI } from '../../shared/Urls'

export const ADD_BOOKMARK = 'ADD_BOOKMARK';
export const REMOVE_BOOKMARK = 'REMOVE_BOOKMARK';
export const REMOVE_ALL_BOOKMARK = 'REMOVE_ALL_BOOKMARK';

export const addBookmark = (post) => ({
    type: ADD_BOOKMARK,
    post: post
});

export const removeBookmark = (postId) => ({
    type: REMOVE_BOOKMARK,
    postId: postId
});

export const removeAllBookmark = () => ({
    type: REMOVE_ALL_BOOKMARK,
});
