import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';


// Imports: Redux Actions
import { resolveConflict, removePeriod } from '../redux/actions/calenderActions';

// Screen: PeriodDetails
class PeriodDetails extends Component {
    constructor(props) {
        super(props);

        this.state = {
            hasConflict: false,
            period: {}
        }
    }
    render() {
        const { classes } = this.props;
        if (this.state.hasConflict === true || this.state.hasConflict === 'true') {
            const periodConflictsView = this.state.period.conflicts.map((resultValue) => {
                return (
                    <span key={'span_' + resultValue.periodId}>
                        <ListItem className={classes.listItem} key={resultValue.periodId} dense button >
                            <ListItemText id={'text_' + resultValue.periodId}
                                primary={resultValue.courseCode}
                                secondary={resultValue.courseName}
                            />
                            <ListItemSecondaryAction>
                                <Button variant="contained" color="primary" onClick={() => this.updatePeriod(resultValue)}>
                                    Select
                                </Button>
                            </ListItemSecondaryAction>
                        </ListItem>
                        <Divider />
                        <br />
                    </span>
                )
            });
            return (
                <React.Fragment>
                    <span style={{ fontWeight: 'bold', fontSize: 20, backgroundColor: '#f9ecf2' }}>Resolve conflict by selecting one module.</span>
                    <List>
                        {periodConflictsView}
                    </List>
                </React.Fragment>
            )
        } else {
            return (
                <Card className={classes.root}>
                    <CardActionArea>
                        <CardContent>
                            <Typography style={{ textAlign: 'center' }} gutterBottom variant="h5" component="h2">
                                {this.state.period.courseCode}
                            </Typography>
                            <Typography style={{ textAlign: 'center' }} variant="subtitle1" color="textSecondary" component="p">
                                {this.state.period.courseName}
                            </Typography>
                            <Divider />
                            <Typography>
                                <br />
                                <br />
                            </Typography>
                            <Typography style={{ textAlign: 'center' }} variant="body2" color="textSecondary" component="p">
                                <span style={{ fontWeight: 'bold' }}>Venue: </span> {this.state.period.venueName}
                            </Typography>
                        </CardContent>
                    </CardActionArea>
                    <CardActions>
                        <Button size="small" variant="contained" onClick={() => { this.props.history.push('/') }}>
                            Back to Timetable
                    </Button>
                        <Button size="small" variant="contained" onClick={() => { this.goToChat() }}>
                            Open Chatroom
                    </Button>
                        <Button size="small" variant="contained" color="secondary" onClick={() => { this.deletePeriod() }}>
                            Delete Period
                    </Button>
                    </CardActions>
                </Card>
            )
        }
    }

    updatePeriod(period) {
        this.props.resolveConflict(period.periodId, period);
        this.setState({ hasConflict: false, period: period });
    }

    deletePeriod() {
        this.props.removePeriod(this.state.period.periodId);
        this.props.history.push('/')
    }

    goToChat() {
        this.props.history.push('/Posts/' + this.state.period.courseId + '/code/' + this.state.period.courseCode);
    }

    getPeriod(periodId) {
        for (var i = 0; i < this.props.timetable.length; i++) {
            if (this.props.timetable[i].monday.periodId === periodId) {
                return this.props.timetable[i].monday;
            }
            if (this.props.timetable[i].tuesday.periodId === periodId) {
                return this.props.timetable[i].tuesday;
            }
            if (this.props.timetable[i].wednesday.periodId === periodId) {
                return this.props.timetable[i].wednesday;
            }
            if (this.props.timetable[i].thursday.periodId === periodId) {
                return this.props.timetable[i].thursday;
            }
            if (this.props.timetable[i].friday.periodId === periodId) {
                return this.props.timetable[i].friday;
            }
        }
        return {};
    };

    componentDidMount() {
        const { params } = this.props.match;
        const period = this.getPeriod(params.id);
        this.setState({ hasConflict: period.hasConflict, period: period });


    }
}

// Styles
const styles = theme => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(0),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        fontSize: 10,
    },
});

// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        timetable: state.calenderReducer.timetable,
        university: state.calenderReducer.university
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action 
    return {
        resolveConflict: (periodId, course) => dispatch(resolveConflict(periodId, course)),
        removePeriod: (periodId) => dispatch(removePeriod(periodId))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(withRouter(PeriodDetails)));