import {
  ADD_POST,
  ADD_POST_RESPONSE,
  ERROR_ADDING_POST,
  GET_POSTS,
  RECEIVE_POSTS,
  ERROR_RECEIVING_POSTS,
  LIKE_POST,
  LIKE_POST_RESPONSE,
  ERROR_LIKING_POST,
  RESET_POSTS
} from '../actions/postActions'

// Initial State
const initialState = {
  posts: [],
  response: '',
  error: '',
  isFetchingPosts: false,
  hasHttpErrorFetchingPosts: false,
  likePostPendingResponse: false,
  hasHttpErrorLikingPost: false,
  addPostPending: false,
  hasHttpErrorAddingPost: false,
  categoryId: ''
};

// Reducers (Modifies The State And Returns A New State)
const postReducer = (state = initialState, action) => {
  switch (action.type) {

    //*********************************ADD_POST********************************************* */
    case ADD_POST: {
      return {
        ...state,
        error: '',
        hasHttpErrorAddingPost: false,
        addPostPending: true
      }
    }

    case ADD_POST_RESPONSE: {
      return {
        ...state,
        error: '',
        posts: [action.data, ...state.posts],
        hasHttpErrorAddingPost: false,
        addPostPending: false
      }
    }

    case ERROR_ADDING_POST: {
      return {
        ...state,
        error: action.error,
        hasHttpErrorAddingPost: true,
        addPostPending: false
      }
    }
    //********************************END:: ADD_POST ::END***************************************** */
    //*********************************GET_POSTS********************************************* */
    case GET_POSTS: {
      return {
        ...state,
        error: '',
        hasHttpErrorFetchingPosts: false,
        isFetchingPosts: true,
        categoryId: action.categoryId
      }
    }

    case RESET_POSTS: {
      return {
        ...state,
        error: '',
        posts: []
      }
    }

    case RECEIVE_POSTS: {
      return {
        ...state,
        error: '',
        posts: appendOrReplacePosts(state.posts, action.data),
        hasHttpErrorFetchingPosts: false,
        isFetchingPosts: false
      }
    }

    case ERROR_RECEIVING_POSTS: {
      return {
        ...state,
        error: action.error,
        hasHttpErrorFetchingPosts: true,
        isFetchingPosts: false
      }
    }
    //********************************END:: GET_POSTS ::END***************************************** */
    //*********************************LIKE_POST**************************************************** */

    case LIKE_POST: {
      return {
        ...state,
        error: '',
        hasHttpErrorLikingPost: false,
        likePostPendingResponse: true
      }
    }

    case LIKE_POST_RESPONSE: {
      return {
        ...state,
        error: '',
        response: action.data,
        hasHttpErrorLikingPost: false,
        likePostPendingResponse: false
      }
    }

    case ERROR_LIKING_POST: {
      return {
        ...state,
        error: action.error,
        hasHttpErrorLikingPost: true,
        likePostPendingResponse: false
      }
    }
    //********************************END:: LIKE_POST ::END**************************************** */
    // Default
    default: {
      return state;
    }
  }
};

const appendOrReplacePosts = (oldPosts, newPosts) => {
  if (oldPosts.length > 0 && newPosts.length > 0) {
    if (oldPosts[0].categoryId === newPosts[0].categoryId) {
      return [...oldPosts, ...newPosts];
    } else {
      return [...newPosts];
    }
  } else if (newPosts.length > 0) {
    return [...newPosts];
  } else {
    return [...oldPosts];
  }
};

// Exports
export default postReducer;