import { BASE_URL_CoursesAPI } from '../../shared/Urls'

export const GET_CAMPUSES = 'GET_CAMPUSES';
export const RECEIVE_CAMPUSES = 'RECEIVE_CAMPUSES';
export const ERROR_GETTING_CAMPUSES = 'ERROR_GETTING_CAMPUSES';

export const getCampuses = () => ({
    type: GET_CAMPUSES
});

export const receiveCampuses = (data) => ({
    type: RECEIVE_CAMPUSES,
    data: data
});

export const errorReceivingCampuses = (error) => ({
    type: ERROR_GETTING_CAMPUSES,
    error: error
});

export function fetchCampuses(universityId) {
    var url = BASE_URL_CoursesAPI + 'Campuses/universities/' + universityId;
    return dispatch => {
        dispatch(getCampuses());
        return fetch(url, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(json => {
                dispatch(receiveCampuses(json));
            }).catch(err => {
                dispatch(errorReceivingCampuses(err));
            });
    }
}
