import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Snackbar from '@material-ui/core/Snackbar';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import CloseIcon from '@material-ui/icons/Close';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import AddCommentIcon from '@material-ui/icons/AddComment';
import BookmarkIcon from '@material-ui/icons/Bookmark';
import Chip from '@material-ui/core/Chip';
import AttachmentIcon from '@material-ui/icons/Attachment';
import TextField from '@material-ui/core/TextField';

//import screens
import MediaCard from './MediaCard';
import ActivitySpinner from './ActivitySpinner'
import Comments from './Comments'

// Imports: Redux Actions
import { fetchCommentsForPost, AddComment } from '../redux/actions/commentActions';
import { addBookmark, removeBookmark, removeAllBookmark } from '../redux/actions/bookmarkActions';
import { likePostById } from '../redux/actions/postActions';
import { uploadFileToStorage } from '../redux/actions/uploadActions';

class PostCard extends Component {
    constructor(props) {
        super(props);
        this.fileUpload = React.createRef();
        this.showFileUpload = this.showFileUpload.bind(this);
        this.onFileChange = this.onFileChange.bind(this);
        this.state = {
            hasConflict: false,
            post: {},
            showSnackbar: false,
            snackbarText: '',
            showAddComment: false,
            fileIsAttached: false,
            fileName: '',
            documentObject: {},
        }
    }
    render() {
        const { classes } = this.props;
        const date = new Date(this.props.post.addedDate);
        const displayDate = date.toDateString() + ' ' + date.toLocaleTimeString();

        return (
            <div className={classes.cardStyle}>
                <Divider />
                <Snackbar
                    anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                    key={'course_snackbar'}
                    open={this.state.showSnackbar}
                    onClose={() => { this.handleClose() }}
                    message={this.state.snackbarText}
                    autoHideDuration={3000}
                    action={
                        <React.Fragment>
                            <Button color="secondary" size="small" onClick={() => { this.handleClose() }}>
                                DISMISS
                                </Button>
                            <IconButton
                                aria-label="close"
                                color="inherit"
                                onClick={() => { this.handleClose() }}
                            >
                                <CloseIcon />
                            </IconButton>
                        </React.Fragment>
                    }
                />

                <Card className={classes.root}>
                    <CardHeader
                        avatar={
                            <Avatar aria-label="recipe" className={classes.avatar}>
                                <AccountCircleIcon />
                            </Avatar>
                        }
                        action={
                            <IconButton aria-label="settings">
                                <MoreVertIcon />
                            </IconButton>
                        }
                        title={this.getUserFullNames()}
                        subheader={displayDate}
                    />
                    <Divider />

                    <CardContent>
                        <Typography variant="body2" color="textSecondary" component="p">
                            {this.props.post.body}
                        </Typography>
                    </CardContent>
                    <CardContent>
                        {((this.props.post.files !== null && this.props.post.files.length > 0)) &&
                            <MediaCard
                                file={this.props.post.files[0]}
                            />
                        }
                    </CardContent>
                    <Divider />
                    <CardActions style={{ backgroundColor: '#f9ecf2' }} disableSpacing>
                        <IconButton aria-label="Like this post" style={{ color: this.state.likeButtonBackgroundColor }} onClick={() => { this.handlePostLike() }}>
                            <FavoriteIcon />
                        </IconButton>
                        <IconButton aria-label="Add Comment" style={{ color: 'black' }} onClick={() => { this.toggleShowAddComment() }}>
                            <AddCommentIcon />
                        </IconButton>
                        <IconButton edge="end" aria-label="Add to bookmark" onClick={() => { this.handleBookmarkClick() }}>
                            <BookmarkIcon style={{ color: this.state.bookmarkButtonBackgroundColor }} />
                        </IconButton>
                        <span style={{ color: '#f9ecf2' }}>__</span>
                        {(this.props.post.totalComments > 0) &&
                            <React.Fragment>
                                <Chip variant="outlined" color="primary" size="small" label={this.props.post.totalComments + " comments"} clickable onClick={() => { this.toggleComments() }} />
                                <span style={{ color: '#f9ecf2' }}>_</span>
                            </React.Fragment>
                        }
                        {(this.props.post.likes.length > 0) &&
                            <Chip variant="outlined" size="small" label={this.props.post.likes.length + " likes"} />
                        }
                    </CardActions>

                </Card>
                <div>
                    {(this.state.showAddComment === true) &&
                        <div>
                            <TextField
                                id="standard-full-width"
                                label=""
                                style={{ margin: 8 }}
                                placeholder="Add your comment..."
                                fullWidth
                                multiline
                                rows="4"
                                margin="normal"
                                onChange={(text) => this.onChangeText(text)}
                                value={this.state.postText}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                            />

                            {(this.state.fileIsAttached === false) &&
                                <React.Fragment>
                                    <div>
                                        <input style={{ display: "none" }} id="postFileId" type="file" ref={this.fileUpload} onChange={this.onFileChange} />
                                    </div>

                                    <div>
                                        <Chip
                                            icon={<AttachmentIcon />}
                                            label="attach file(image/pdf/video/etc)"
                                            clickable
                                            color="primary"
                                            size="small"
                                            variant="outlined"
                                            onClick={() => { this.showFileUpload() }}
                                        />
                                    </div>
                                </React.Fragment>
                            }

                            {(this.state.fileIsAttached === true) &&
                                <React.Fragment>
                                    <Chip
                                        icon={<AttachmentIcon />}
                                        label={this.state.fileName}
                                        onDelete={() => { this.removeFile() }}
                                        color="secondary"
                                        variant="outlined"
                                    />
                                    <span><br /></span>
                                </React.Fragment>
                            }


                            <div style={{ width: '50%' }}>
                                <span><br /></span>
                                <Button variant="contained" color="primary" onClick={() => { this.sentComment() }}>
                                    Add Comment
                                    </Button>
                            </div>

                            <br />
                        </div>
                    }
                    {(this.state.addPostPending === true) &&

                        <div>
                            <span style={{ color: 'green', fontWeight: 'bold' }}>Adding your comment...</span>
                            <ActivitySpinner />
                        </div>
                    }
                </div>

                {(this.state.showComments === true) &&
                    <div className={classes.containerComments}>
                        <span style={{ width: '20%' }}></span>
                        <div style={{ width: '80%' }}>
                            <Comments postId={this.props.post.id} />
                        </div>
                    </div>
                }
            </div>
        );
    }

    onFileChange(e) {
        this.setState({ fileIsAttached: true, fileName: e.target.files[0].name, documentObject: e.target.files[0] })
    }

    showFileUpload() {
        this.fileUpload.current.click();
    }

    toggleShowAddComment() {
        this.setState({ showAddComment: !this.state.showAddComment });
    }

    showSnackbar(text) {
        this.setState({ showSnackbar: true, snackbarText: text });
    };

    handleClose() {
        this.setState({ showSnackbar: false, snackbarText: '' });
    };
    removeFile() {
        this.setState({ fileIsAttached: false, fileName: '' })
    }

    getUserFullNames() {
        if (this.props.post.addedBy === null || this.props.post.addedBy === 'null' || this.props.post.addedBy === undefined) {
            return 'Anonymous User';
        } else {
            return this.props.post.addedBy.fullnames;
        }
    }

    handlePostLike() {
        this.props.likePostById(this.props.post.id, this.props.user).then(() => {
            if (this.state.likeButtonBackgroundColor === 'black') {
                this.setState({ likeButtonBackgroundColor: 'green', totalLikes: this.state.totalLikes + 1 })
            } else {
                this.setState({ likeButtonBackgroundColor: 'black', totalLikes: this.state.totalLikes - 1 })
            }
        });
    }

    handleBookmarkClick() {
        if (this.existOnBookmarks() === true) {
            this.setState({ bookmarkButtonBackgroundColor: 'black' })
            this.props.removeBookmark(this.props.post.id);
        } else {
            this.setState({ bookmarkButtonBackgroundColor: 'green' })
            this.props.addBookmark(this.props.post);
        }
    }

    existOnBookmarks() {
        let exist = false;
        const list = this.props.bookmarkedPosts;
        for (var i = 0; i < list.length; i++) {
            if (list[i].id === this.props.post.id) {
                exist = true;
            }
        }
        return exist;
    }

    existOnLikes() {
        var exist = false;
        const list = this.props.post.likes;
        for (var i = 0; i < list.length; i++) {
            if (list[i].userId === this.props.user.userId) {
                exist = true;
            }
        }
        return exist;
    }

    sentComment() {
        if (this.state.commentText.length < 1) {
            this.showSnackbar('Input must be at least 1 letter');
            return;
        } else {
            this.setState({ addPostPending: true })
            if (this.state.fileIsAttached === false) {
                this.props.AddComment(this.props.post.id, {
                    body: this.state.commentText,
                    addedBy: this.props.user
                }).then(() => {
                    this.setState({
                        addPostPending: false,
                        commentText: '',
                        showAddComment: false,
                        showComments: true,
                        totalComments: this.state.totalComments + 1
                    })

                })
            } else {
                this.props.uploadFileToStorage(this.state.documentObject)
                    .then(() => {
                        this.props.AddComment(this.props.post.id, {
                            body: this.state.commentText,
                            addedBy: this.props.user,
                            files: [this.props.uploadResponse]
                        }).then(() => {
                            this.setState({
                                commentText: '',
                                fileIsAttached: false,
                                fileName: '',
                                addPostPending: false,
                                showAddComment: false,
                                showComments: true,
                                totalComments: this.state.totalComments + 1
                            })
                        })
                    });
            }
        }
    }

    onChangeText(text) {
        this.setState({ commentText: text.target.value });
    }

    toggleComments() {
        this.setState({ showComments: !this.state.showComments });
    }

    componentDidMount() {
        var likeText = 'black';
        var bookmarkButtonBackgroundColor = 'black';
        if (this.existOnLikes()) {
            likeText = 'green';
        }
        if (this.existOnBookmarks() === true) {
            bookmarkButtonBackgroundColor = 'green';
        }
        this.setState({ bookmarkButtonBackgroundColor: bookmarkButtonBackgroundColor, likeButtonBackgroundColor: likeText })
    }

}
const useStyles = theme => ({
    root: {
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: 'green',
    },
    containerComments: {
        display: 'flex',
        flexWrap: 'nowrap',
        backgroundColor: 'white',
        flexDirection: 'row',
    },
    cardStyle: {
        borderRadius: 10,
    }
});

// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        isFetchingComments: state.commentReducer.isFetchingComments,
        bookmarkedPosts: state.bookmarkReducer.bookmarkedPosts,
        user: state.userReducer.user,
        uploadResponse: state.uploadReducer.response,
        percent: state.uploadReducer.percent,
        isUploadingFile: state.uploadReducer.isFetching
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action
    return {
        fetchComments: (id) => dispatch(fetchCommentsForPost(id)),
        AddComment: (postId, comment) => dispatch(AddComment(postId, comment)),
        addBookmark: (post) => dispatch(addBookmark(post)),
        removeBookmark: (postId) => dispatch(removeBookmark(postId)),
        removeAllBookmark: () => dispatch(removeAllBookmark()),
        likePostById: (postId, user) => dispatch(likePostById(postId, user)),
        uploadFileToStorage: (obj) => dispatch(uploadFileToStorage(obj))
    };
};

// Exports
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(PostCard));
