// Imports: Dependencies
import React, { Component, Fragment } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';

//import screens
import ActivitySpinner from './ActivitySpinner'
import CommentCard from './CommentCard';

// Imports: Redux Actions
import { fetchCommentsForPost } from '../redux/actions/commentActions';

// Screen: Comments
class Comments extends Component {
    render() {

        if (this.props.isFetchingComments === true) {
            return (
                <ActivitySpinner />
            )
        } else {

            const commentsView = this.props.comments.map((resultValue) => {
                return (
                    <CommentCard comment={resultValue} />
                )
            });

            return (
                <div>
                    {commentsView}
                </div >
            )
        }
    }

    componentDidMount() {
        this.props.fetchComments(this.props.postId);
    }
}

// Styles
const useStyles = theme => ({
    container: {
        margin: 1,
        display: 'flex',
        flexWrap: 'nowrap'
    },
});

// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        comments: state.commentReducer.comments,
        isFetchingComments: state.commentReducer.isFetchingComments
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action
    return {
        fetchComments: (id) => dispatch(fetchCommentsForPost(id))
    };
};

// Exports
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(Comments));