// Imports: Dependencies
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import Divider from '@material-ui/core/Divider';
import AdSense from 'react-adsense';
//import screens
import PostCard from './PostCard';

// Imports: Redux Actions
import { addBookmark, removeBookmark, removeAllBookmark } from '../redux/actions/bookmarkActions';

// Screen: Comments
class Bookmarks extends Component {
    constructor(props) {
        super(props);

        this.state = {
            nextStartLoadDate: null,
        }
    }
    render() {

        const postsView = this.props.bookmarkedPosts.map((resultValue) => {
            return (
                <div key={'post_view_' + resultValue.id}>
                    <PostCard post={resultValue} />
                </div>
            )
        });

        return (
            <div>
                <span style={{ fontWeight: 'bold', fontSize: 20 }}>Posts you bookmarked.</span>
                <Divider />
                <br />
                {postsView}
                <AdSense.Google
  client='ca-pub-7292810486004926'
  slot='7806394673'
/>
            </div >
        )
    }



    componentDidMount() {
    }
}

// Styles


// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        bookmarkedPosts: state.bookmarkReducer.bookmarkedPosts,
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action
    return {
        addBookmark: (post) => dispatch(addBookmark(post)),
        removeBookmark: (postId) => dispatch(removeBookmark(postId)),
        removeAllBookmark: () => dispatch(removeAllBookmark())
    };
};

// Exports
export default connect(mapStateToProps, mapDispatchToProps)(Bookmarks);