import {
    POST_FEEDBACK,
    ERROR_POSTING_FEEDBACK,
    POST_FEEDBACK_RESPONSE
} from '../actions/feedbackActions'

// Initial State
const initialState = {
  response: '',
  error: '',
  isFetching:false,
  hasHttpError:false
};

// Reducers (Modifies The State And Returns A New State)
const feedbackReducer = (state = initialState, action) => {
  switch (action.type) {

    case POST_FEEDBACK: {
      return {
        ...state,
        error: '',
        hasHttpError: false,
        isFetching:true
      }
    }

    case POST_FEEDBACK_RESPONSE: {
      return {
        ...state,
        error: '',
        response: action.data,
        hasHttpError: false,
        isFetching:false
      }
    }

    case ERROR_POSTING_FEEDBACK: {
      return {
        ...state,
        error: action.error,
        hasHttpError: true,
        isFetching:false
      }
    }

    // Default
    default: {
      return state;
    }
  }
};


// Exports
export default feedbackReducer;