import {
    UPLOAD_FILE,
    UPLOAD_FILE_RESPONSE,
    UPLOAD_FILE_PROGRESS,
    ERROR_UPLOADING_FILE
} from '../actions/uploadActions'

// Initial State
const initialState = {
    response: '',
    error: '',
    isFetching: false,
    hasHttpError: false,
    percent:0
};

// Reducers (Modifies The State And Returns A New State)
const uploadReducer = (state = initialState, action) => {
    switch (action.type) {

        case UPLOAD_FILE: {
            return {
                ...state,
                error: '',
                hasHttpError: false,
                isFetching: true
            }
        }

        case UPLOAD_FILE_PROGRESS: {
            return {
                ...state,
                error: '',
                hasHttpError: false,
                isFetching: true,
                percent: action.percent
            }
        }

        case UPLOAD_FILE_RESPONSE: {
            return {
                ...state,
                error: '',
                response: action.data,
                hasHttpError: false,
                isFetching: false
            }
        }

        case ERROR_UPLOADING_FILE: {
            return {
                ...state,
                error: action.error,
                hasHttpError: true,
                isFetching: false
            }
        }

        // Default
        default: {
            return state;
        }
    }
};


// Exports
export default uploadReducer;