// Imports: Dependencies
import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import ForumIcon from '@material-ui/icons/Forum';
import Divider from '@material-ui/core/Divider';
import { withRouter } from 'react-router-dom';

// Imports: Redux Actions
import { getNumberOfUnreadMessages } from '../redux/actions/calenderActions';

class Chats extends Component {
    render() {
        const { classes } = this.props;
        const coursesView = this.props.courses.map((resultValue) => {
            return (
                <span key={'span_' + resultValue.id}>
                    <ListItem className={classes.listItem} key={resultValue.id} dense button onClick={() => { this.openPosts(resultValue.id, resultValue.code) }}>
                        <ListItemText id={'text_' + resultValue.id} primary={resultValue.code} secondary={resultValue.name} />
                        <ListItemSecondaryAction>
                            <IconButton edge="end" aria-label="comments">
                                <ForumIcon />
                            </IconButton>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <Divider />
                    <br />
                </span>
            )
        });

        return (
            <div>
                <Button size="small" variant="contained" color="primary" onClick={() => { this.addModule() }}>
                    Add or Remove module
                </Button>

                <Divider />
                < span > Total number of modules you doing is {this.props.courses.length}</span>
                <Divider />
                {(this.props.university !== null && this.props.university !== undefined && this.props.university.id !== undefined
                    && this.props.university.id !== '' && this.props.university.id !== '0000-0000-000000000000') &&
                    <span key={'span_' + this.props.university.id}>
                        <ListItem className={classes.listItem} key={this.props.university.id} dense button onClick={() => { this.openPosts(this.props.university.id, this.props.university.name) }}>
                            <ListItemText
                                id={'text_' + this.props.university.id}
                                primary={this.props.university.name}
                                secondary={'Send message to all students in your institution'}
                            />
                            <ListItemSecondaryAction>
                                <IconButton edge="end" aria-label="comments">
                                    <ForumIcon />
                                </IconButton>
                            </ListItemSecondaryAction>
                        </ListItem>
                        <Divider />
                    </span>
                }

                <Divider />
                <List>
                    {coursesView}
                </List>
            </div>
        )
    }

    openPosts(postId, code) {
        this.props.history.push('/Posts/' + postId + '/code/' + code);
    }

    addModule() {
        if (this.props.university === null || this.props.university === undefined || this.props.university.id === undefined
            || this.props.university.id === '' || this.props.university.id === '0000-0000-000000000000') {
            this.props.history.push('/University')
        } else if (this.props.university.timetablePerCampus === true || this.props.university.timetablePerCampus === 'true') {
            this.props.history.push('/Campus/' + this.props.university.id)
        } else {
            this.props.history.push('/institutions/' + this.props.university.id + '/type/universities')
        }
    }

}

// Styles
const useStyles = theme => ({
    root: {
        width: '100%'
    },
    listItem: {
        width: '100%',
        backgroundColor: '#f9ecf2'
    },
});

// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        courses: state.calenderReducer.courses,
        university: state.calenderReducer.university
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action
    return {
        getNumberOfUnreadMessages: (state) => dispatch(getNumberOfUnreadMessages(state))
    };
};

// Exports
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(withRouter(Chats)));