// Imports: Dependencies
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import SchoolIcon from '@material-ui/icons/School';
import Divider from '@material-ui/core/Divider';
import { withRouter } from 'react-router-dom';

// Imports: Redux Actions
import { setUniversity } from '../redux/actions/calenderActions';
import { fetchUniversities } from '../redux/actions/universityActions';

//import screens
import ActivitySpinner from './ActivitySpinner'

class University extends Component {
    render() {
        const { classes } = this.props;
        const universitiesView = this.props.universities.map((resultValue) => {
            return (
                <span key={'span_' + resultValue.id}>
                    <ListItem className={classes.listItem} key={resultValue.id} dense button onClick={() => { this.goToCampusesOrModules(resultValue) }}>
                        <ListItemText id={'text_' + resultValue.id} primary={resultValue.name} />
                        <ListItemSecondaryAction>
                            <IconButton edge="end" aria-label="comments">
                                <SchoolIcon />
                            </IconButton>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <Divider />
                    <br />
                </span>
            )
        });
        if (this.props.isFetching === true || this.props.isFetching === 'true') {
            return (
                <ActivitySpinner />
            )
        } else {
            return (
                <React.Fragment>
                    <span style={{ fontWeight: 'bold', fontSize: 20 }}>Click your institution below.</span>
                    <Divider />
                    <br />
                    <List className={classes.root}>
                        {universitiesView}
                    </List>
                </React.Fragment>
            )
        }

    }

    goToCampusesOrModules(university) {
        setUniversity(university);
        if (university.timetablePerCampus === true || university.timetablePerCampus === 'true') {
            this.props.history.push('/Campus/' + university.id)
        } else {
            this.props.history.push('/institutions/' + university.id + '/type/universities')
        }
    }

    componentDidMount() {
        this.props.fetchUniversities();
    }

}

const useStyles = theme => ({
    root: {
        width: '100%'
    },
    listItem: {
        width: '100%',
        backgroundColor: '#f9ecf2'
    },
});

// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        universities: state.universitiesReducer.universities,
        isFetching: state.universitiesReducer.isFetching,
        error: state.universitiesReducer.error
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action
    return {
        fetchUniversities: () => dispatch(fetchUniversities()),
        updateUniversity: (university) => dispatch(setUniversity(university))
    };
};

// Exports
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(withRouter(University)));