
import { BASE_URL_CoursesAPI } from '../../shared/Urls'

export const REQUEST_CALENDER = 'REQUEST_CALENDER';
export const RECEIVE_CALENDER = 'RECEIVE_CALENDER';
export const ERROR_REQUESTING_CALENDER = 'ERROR_REQUESTING_CALENDER';
export const REPORT_CALENDER = 'REPORT_CALENDER';
export const ADD_MODULE = 'ADD_MODULE';
export const REMOVE_MODULE = 'REMOVE_MODULE';
export const GET_CALENDER = 'GET_CALENDER';
export const REMOVE_PERIOD = 'REMOVE_PERIOD';
export const RESOLVE_CONFLICT = 'RESOLVE_CONFLICT';
export const SEARCH_MODULES = 'SEARCH_MODULES';
export const RECEIVE_MODULES = 'RECEIVE_MODULES';
export const ERROR_RECEIVING_MODULES = 'ERROR_RECEIVING_MODULES';
export const GET_UNREAD_MESSAGES_COUNT = 'GET_UNREAD_MESSAGES_COUNT';
export const SET_UNIVERSITY = 'SET_UNIVERSITY';


export const receiveModules = (data) => ({
    type: RECEIVE_MODULES,
    data: data
});

export const searchModules = () => ({
    type: SEARCH_MODULES
});

export const errorReceivingModules = (error) => ({
    type: ERROR_RECEIVING_MODULES,
    error: error
});

export const requestCalender = (data) => ({
    type: REQUEST_CALENDER,
    data: data
});

export const getCalender = () => ({
    type: GET_CALENDER
});

export const receiveCalender = (data) => ({
    type: RECEIVE_CALENDER,
    data: data
});

export const errorRequestingCalender = (error) => ({
    type: REQUEST_CALENDER,
    error: error
});

export const reportCalender = (data) => ({
    type: REPORT_CALENDER,
    data: data
});

export const addModule = (course) => ({
    type: ADD_MODULE,
    course: course
});

export const getNumberOfUnreadMessages = (courses) => ({
    type: GET_UNREAD_MESSAGES_COUNT,
    courses: courses
});

export const removeModule = (courseId) => ({
    type: REMOVE_MODULE,
    courseId: courseId
});

export const removePeriod = (periodId) => ({
    type: REMOVE_PERIOD,
    periodId: periodId
});

export const resolveConflict = (periodId, course) => ({
    type: RESOLVE_CONFLICT,
    periodId: periodId,
    course: course
});

export const setUniversity = (university) => ({
    type: SET_UNIVERSITY,
    university: university
});

export function fetchUniversityFromBackEnd(universityId) {
    var url = BASE_URL_CoursesAPI + 'Universities/' + universityId;
    return dispatch => {
        dispatch(searchModules());
        return fetch(url, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(json => {
                dispatch(setUniversity(json));
            }).catch(err => {
                dispatch(setUniversity({
                    id: '0000-0000-000000000000',
                    name: 'Your Institution',
                    adsDetails: {
                        bannerAd: 'ca-app-pub-5631714136940575/3298705317',
                        nativeAdvancedAd: 'ca-app-pub-5631714136940575/4838053665'
                    }
                }));
            });
    }
}

export function fetchSearchModules(campusId, body, routPath) {
    var url = BASE_URL_CoursesAPI + 'Courses/' + routPath + '/' + campusId + '/search';
    return dispatch => {
        dispatch(searchModules());
        return fetch(url, {
            method: 'POST',
            body: JSON.stringify(body),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(json => {
                dispatch(receiveModules(json));
            }).catch(err => {
                dispatch(errorReceivingModules(err));
            });
    }
}

export function fetchCalender(body) {
    var url = BASE_URL_CoursesAPI + 'Timetable/universities/' + body.universityId + '/slots';
    return dispatch => {
        dispatch(requestCalender());
        return fetch(url, {
            method: 'POST',
            body: JSON.stringify(body.coursesIds),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(json => {
                dispatch(receiveCalender(json));
            }).catch(err => {
                dispatch(errorRequestingCalender(err));
            });
    }
}

export function getTimetable(body) {
    return (dispatch, getState) => {
        if (body.universityId === '0000-0000-000000000000') {
            return dispatch(receiveCalender([]));
        }
        const mustRefreshCalender = getState().calenderReducer.mustRefreshCalender;
        if (mustRefreshCalender === true) {
            return dispatch(fetchCalender(body))
        } else {
            dispatch(getCalender());
            var timetable = getState().calenderReducer.timetable;
            return dispatch(receiveCalender(timetable));
        }
    }
}

export function fetchAndSetUniversity(universityId) {
    return (dispatch, getState) => {
        const university = getState().calenderReducer.university;
        if (university === null) {
            return dispatch(fetchUniversityFromBackEnd(universityId))
        } else if (university.id !== universityId && universityId !== '0000-0000-000000000000') {
            return dispatch(fetchUniversityFromBackEnd(universityId))
        }
    }
}