// Imports: Dependencies
import React, { Component } from 'react';
import { connect } from 'react-redux';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';

// Imports: Redux Actions
import { reportTimetableError } from '../redux/actions/feedbackActions';

//import screens
import ActivitySpinner from './ActivitySpinner'

class Feedback extends Component {
    constructor(props) {
        super(props);

        this.state = {
            selectedId: '',
            feedbackText: '',
            isLoading: false,
            snackbarText: '',
            showSnackbar: false
        }
    }
    render() {
        if (this.state.isLoading === true) {
            return (
                <ActivitySpinner />
            )
        } else {
            const { classes } = this.props;
            const coursesView = this.props.courses.map((resultValue) => {
                return (
                    <div key={'view_' + resultValue.id}>
                        <span key={'span_' + resultValue.id}>
                            <ListItem className={classes.listItem} key={resultValue.id} dense button>
                                <ListItemText id={'text_' + resultValue.id}
                                    primary={resultValue.code}
                                    secondary={resultValue.name}
                                />
                                <ListItemSecondaryAction>
                                    <Button variant="contained" color="primary" onClick={() => { this.selectModuleToReport(resultValue.id) }}>
                                        Report
                                    </Button>
                                </ListItemSecondaryAction>
                            </ListItem>
                            <Divider />
                            <br />
                        </span>
                        {(this.state.selectedId === resultValue.id) &&
                            <React.Fragment>
                                <Divider />
                                <TextField
                                    id="standard-full-width"
                                    label=""
                                    style={{ margin: 8 }}
                                    placeholder="Type here..."
                                    fullWidth
                                    multiline
                                    rows="4"
                                    margin="normal"
                                    onChange={(text) => this.onChangeSearchText(text)}
                                    value={this.state.feedbackText}
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                />
                                <Button variant="contained" color="primary" onClick={() => this.sendFeedBack(resultValue)}>
                                    Send
                                </Button>
                            </React.Fragment>
                        }
                    </div>
                )
            });

            return (
                <React.Fragment>
                    <Snackbar
                        anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                        key={'feedback_snackbar'}
                        open={this.state.showSnackbar}
                        onClose={() => { this.handleClose() }}
                        message={this.state.snackbarText}
                        autoHideDuration={3000}
                        action={
                            <React.Fragment>
                                <Button color="secondary" size="small" onClick={() => { this.handleClose() }}>
                                    DISMISS
                                </Button>
                                <IconButton
                                    aria-label="close"
                                    color="inherit"
                                    onClick={() => { this.handleClose() }}
                                >
                                    <CloseIcon />
                                </IconButton>
                            </React.Fragment>
                        }
                    />

                    <span style={{ fontWeight: 'bold', fontSize: 20 }}> Your feedback is highly appreciated.</span>
                    <Divider />
                    <div key={'view_feedback'}>
                        <span key={'view_listItem_feedback'}>
                            <ListItem className={classes.listItem} key={'ListItem_feedback'} dense button>
                                <ListItemText id={'text_ListItem_feedback'}
                                    primary={'General Feedback/Request'}
                                    secondary={'General Feedback/Request for this App.'}
                                />
                                <ListItemSecondaryAction>
                                    <Button variant="contained" color="primary" onClick={() => { this.selectModuleToReport('general') }}>
                                        Report
                                    </Button>
                                </ListItemSecondaryAction>
                            </ListItem>
                            <Divider />
                            <br />
                        </span>
                        {(this.state.selectedId === 'general') &&
                            <React.Fragment>
                                <Divider />
                                <TextField
                                    id="standard-full-width"
                                    label=""
                                    style={{ margin: 8 }}
                                    placeholder="Type here..."
                                    fullWidth
                                    multiline
                                    rows="4"
                                    margin="normal"
                                    onChange={(text) => this.onChangeSearchText(text)}
                                    value={this.state.feedbackText}
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                />
                                <Button variant="contained" color="primary" onClick={() => this.sendFeedBack({ code: 'General' })}>
                                    Send
                                </Button>
                            </React.Fragment>
                        }
                    </div>
                    <List>
                        {coursesView}
                    </List>
                </React.Fragment>
            )
        }
    }
    showSnackbar(text) {
        this.setState({ showSnackbar: true, snackbarText: text });
    };

    handleClose() {
        this.setState({ showSnackbar: false, snackbarText: '' });
    };
    onChangeSearchText(text) {
        this.setState({ feedbackText: text.target.value });
    }

    selectModuleToReport(id) {
        this.setState({ selectedId: id, feedbackText: '' });
    }

    sendFeedBack(feedbackFor) {
        if (this.state.feedbackText.length < 4) {
            this.showSnackbar('Input must be at least 4 letters');
            return;
        }

        var obj = {
            requestType: 'Feedback',
            title: feedbackFor.code,
            body: this.state.feedbackText,
            universityId: this.props.university.id
        };
        this.setState({ isLoading: true });
        this.props.reportTimetableError(obj).then(() => {
            this.setState({ feedbackText: '', isLoading: false, selectedId: '' });
            this.showSnackbar('Report/Feedback sent successfully.');
        });
    }

}

// Styles
const useStyles = theme => ({
    container: {
        display: 'flex',
        flexWrap: 'nowrap',
        backgroundColor: 'white',
        flexDirection: 'row',
    },
    mainContainer: {
        display: 'flex',
        flexWrap: 'nowrap',
        backgroundColor: 'white',
        flexDirection: 'column',
        alignItems: 'stretch',
    },
    listItem: {
        width: '100%',
        backgroundColor: '#f9ecf2'
    },
});



// Map State To Props (Redux Store Passes State To Component)
const mapStateToProps = (state) => {
    // Redux Store --> Component
    return {
        courses: state.calenderReducer.courses,
        university: state.calenderReducer.university
    };
};

// Map Dispatch To Props (Dispatch Actions To Reducers. Reducers Then Modify The Data And Assign It To Your Props)
const mapDispatchToProps = (dispatch) => {
    // Action
    return {
        reportTimetableError: (body) => dispatch(reportTimetableError(body))
    };
};

// Exports
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(Feedback));