import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import ReactPlayer from 'react-player'
import { withRouter } from 'react-router-dom';
import Chip from '@material-ui/core/Chip';
import VisibilityIcon from '@material-ui/icons/Visibility';
import AttachmentIcon from '@material-ui/icons/Attachment';

// Screen: MediaCard
class MediaCard extends Component {
    render() {
        const { classes } = this.props;
        const isVideoOrAudio = ((this.props.file.contentType.includes('video') === true || this.props.file.contentType.includes('audio') === true));
        const isImage = (this.props.file.contentType.includes('image') === true || this.props.file.contentType.includes('picture') === true);
        if ((isImage === true) || (isVideoOrAudio === true)) {
            return (
                <div>
                    {(isVideoOrAudio === true) &&
                        <ReactPlayer
                            url={'http://sitholewb.co.za/FilesAPI/api/Storage/' + this.props.file.id + '/view'}
                            width='100%'
                            height='100%'
                            playing={false}
                            controls
                        />
                    }
                    {(isImage === true) &&
                        <img src={'http://sitholewb.co.za/FilesAPI/api/Storage/' + this.props.file.id + '/view'} width="100%" height="100%" />
                    }
                </div>
            )
        } else {
            return (
                <div>
                    <Chip
                        icon={<AttachmentIcon />}
                        label={this.props.file.name}
                        clickable
                        color="primary"
                        size="small"
                        variant="outlined"
                        onClick={() => { this.openFile() }}
                        deleteIcon={<VisibilityIcon />}
                    />
                </div>
            )
        }
    }

    openFile() {
        this.props.history.push('/file/' + this.props.file.id + '/view');
    }

}

// Styles
const styles = theme => ({
    playerWrapper: {
        position: 'relative',
        paddingTop: 56 /* Player ratio: 100 / (1280 / 720) */
    },
    reactPlayer: {
        position: 'absolute',
        top: 0,
        left: 0
    }
});
export default withStyles(styles)(withRouter(MediaCard));