import { BASE_URL_CoursesAPI } from '../../shared/Urls'

export const POST_FEEDBACK = 'POST_FEEDBACK';
export const POST_FEEDBACK_RESPONSE = 'POST_FEEDBACK_RESPONSE';
export const ERROR_POSTING_FEEDBACK = 'ERROR_POSTING_FEEDBACK';

export const postFeedback = () => ({
    type: POST_FEEDBACK
});

export const postFeedbackResponse = (data) => ({
    type: POST_FEEDBACK_RESPONSE,
    data: data
});

export const errorPostingFeedback = (error) => ({
    type: ERROR_POSTING_FEEDBACK,
    error: error
});

export function reportTimetableError(body) {
    var url = BASE_URL_CoursesAPI + 'StudentRequests';
    return dispatch => {
        dispatch(postFeedback());
        return fetch(url, {
            method: 'POST',
            body: JSON.stringify(body),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(json => {
                dispatch(postFeedbackResponse(json));
            }).catch(err => {
                dispatch(errorPostingFeedback(err));
            });
    }
}
